// import "bootstrap/dist/css/bootstrap.min.css";
import React from "react";
import { useTranslation } from "react-i18next";
import i18n from "../../translations/i18n";
// import { withNamespaces } from "react-i18next";
import { Form, Badge } from "react-bootstrap";

export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    namespace: "LanguageSelector",
    backgroundColor: "var(--dark)",
    color: "var(--light)",
  };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { userData, signedIn } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { sendRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  const { t } = useTranslation(props.namespace);
  const changeLanguage = (lng) => {
    i18n.changeLanguage(lng);
  };
  const languages = [
    { id: 1, language: "Latvieu", code: "lv" },
    { id: 2, language: "English", code: "en" },
  ];
  const handleLanguageSelect = (e) => {
    changeLanguage(e.target.value);
  };
  const selectLanguage = (language) => {
    changeLanguage(language);

    if (userData !== null && signedIn) {
      sendRequest({
        req: "register user language",
        data: { email: userData.email, language },
      });
    }
  };

  if (1) {
    return (
      <span style={{ paddingLeft: "0.5rem", paddingRight: "0.5rem" }}>
        <Badge
          pill
          style={{
            cursor: "pointer",
            backgroundColor: props.backgroundColor,
            color: props.color,
            marginBottom: "0.3rem",
          }}
          onClick={() => {
            selectLanguage("en");
          }}
        >
          en
        </Badge>{" "}
        <Badge
          pill
          style={{
            cursor: "pointer",
            backgroundColor: props.backgroundColor,
            color: props.color,
            marginBottom: "0.3rem",
          }}
          onClick={() => {
            selectLanguage("lv");
          }}
        >
          lv
        </Badge>
      </span>
    );
  } else {
    return (
      <Form.Control
        as="select"
        id="languageSelector"
        onChange={handleLanguageSelect}
        custom
      >
        <option key="0" value={props.language}>
          {t("Language")}
        </option>
        {languages.map((el) => (
          <option key={el.id} value={el.code}>
            {el.language}
          </option>
        ))}
      </Form.Control>
    );
  }
};
