/**
 * CSS
 ============================================================================ */
// import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";
// import "bootstrap/dist/css/bootstrap.min.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { Form, Table } from "react-bootstrap";
import { Formik } from "formik";
import { useTranslation } from "react-i18next";

/**
 * User imports
 ============================================================================ */
import Pagination from "../pagination";
import Search from "../search";

/**
 * Default export
 */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    request: "get currencies", // "register currency",
    namespace: "Currencies",
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const {
    currencies,
    settings,
    countCurrencies,
    activePageCurrencies,
    currenciesPerPage,
    currencyAbbr,
    userData,
  } = props.barrelGetters;
  const {
    setCurrencyAbbr,
    setResponseProps,
    setCurrencyRate,
  } = props.barrelSetters;
  const {
    sendRequest,
    showRequestStatus,
    getCurrencies,
    findCurrencies,
    formatExchangeRate,
    getObjectArrayElementIndex,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Request status
    ============================================================================ */
  // const requestStatus = getRequestStatus(props.request);

  /**
   * Destructured vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */

  // const indication = () => {
  //   return "THE TABLE IS EMPTY";
  // };

  const onCurrencySelected = (e) => {
    setCurrencyAbbr(e.target.id);
    sendRequest({
      req: "register user currency",
      data: { email: userData.email, currency_id: e.target.id },
    });

    // Fetch the selected currency rate
    const id = getObjectArrayElementIndex(currencies, "abbr", e.target.id);
    setCurrencyRate(currencies[id].rate);
  };

  const onPageSelect = (number) => {
    // console.log("on page select", number);
    setResponseProps(null);
    switch (number) {
      case "First":
        number = 1;
        break;
      case "Prev":
        number = activePageCurrencies > 1 ? activePageCurrencies - 1 : 1;
        break;
      case "Next":
        number =
          activePageCurrencies < Math.ceil(countCurrencies / currenciesPerPage)
            ? activePageCurrencies + 1
            : Math.ceil(countCurrencies / currenciesPerPage);
        break;
      case "Last":
        number = Math.ceil(countCurrencies / currenciesPerPage);
        break;
      default:
        // number = number;
        break;
    }
    getCurrencies(number);
  };

  let cnt = 0;

  /**
   * Form
   ============================================================================ */
  const CurrenciesForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <>
      <div style={{ marginBottom: "1rem" }}>
        <Search
          placeholder={t("Find currency...")}
          onSubmit={findCurrencies}
          {...args}
        />
      </div>
      <Table responsive striped bordered hover size="sm">
        <thead>
          <tr>
            <th style={{ textAlign: "center" }}>{t("Currency")}</th>
            <th style={{ textAlign: "center" }}>{t("Abbr")}</th>
            <th style={{ textAlign: "center" }}>{t("Region")}</th>
            <th style={{ textAlign: "center" }}>{t("Rate")}</th>
          </tr>
        </thead>
        <tbody>
          {currencies !== null &&
            currencies.map((currency, keyCurrency) => {
              cnt++;
              // console.log("CURRENCY", currency);

              const { name, abbr, rate, region } = currency;

              if (
                typeof settings["currencies"] === "undefined" ||
                settings["currencies"] === null ||
                settings["currencies"].length === 0 ||
                settings["currencies"].includes(abbr)
              ) {
                // valid currency - continue
              } else {
                return ""; // invalid currency
              }

              return (
                <tr key={keyCurrency}>
                  <td style={{ textAlign: "left", paddingLeft: "1rem" }}>
                    <Form.Check
                      id={abbr}
                      type="radio"
                      checked={currencyAbbr === abbr}
                      aria-label="option 1"
                      label={name}
                      custom
                      inline
                      onChange={onCurrencySelected}
                      name="selected-currency"
                    />
                  </td>
                  <td style={{ textAlign: "center" }}>{abbr}</td>
                  <td style={{ textAlign: "left" }}>{region}</td>
                  <td style={{ textAlign: "right" }}>
                    {formatExchangeRate(rate)}
                  </td>
                </tr>
              );
            })}
        </tbody>
      </Table>

      {showRequestStatus(
        "get currencies / SELECT currencies",
        cnt,
        t("Loading..."),
        t("No currencies"),
        "finished"
      )}

      <Pagination
        {...args}
        size="sm"
        countItems={countCurrencies}
        itemsPerPage={currenciesPerPage}
        active={activePageCurrencies}
        onClick={onPageSelect}
      />
    </>
  );

  /**
   * Formik return
   ============================================================================ */
  // onSubmit={onSubmit}
  return (
    <Formik
      component={CurrenciesForm}
      initialValues={{
        curr_abbr: "",
        curr_title: "",
        curr_region: "",
        curr_rate: 1.0,
      }}
    />
  );
};
