/**
 * System imports
 ============================================================================ */
import React, { useState, useEffect } from "react";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import { Formik } from "formik";
import { Form, InputGroup } from "react-bootstrap";
import { Trans, useTranslation } from "react-i18next";
import * as yup from "yup";

/**
  * User imports
  ============================================================================ */
import SubmitButton from "../submitButton";
import Card from "../card";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = { request: "register user", namespace: "SignUp" };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   */
  const {
    language,
    signUpFirstName,
    signUpLastName,
    signUpEmail,
    signUpPassword,
  } = props.barrelGetters;
  const {
    setSignUpFirstName,
    setSignUpLastName,
    setSignUpEmail,
    setSignUpPassword,
  } = props.barrelSetters;
  const { getReviser, getWorkplace, sendRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Hooks
   */
  const [passwordVisible, setPasswordVisible] = useState(false);

  /**
   * Custom hooks
   */
  const { t } = useTranslation(props.namespace);

  /**
   * Create refs
   */
  const refFirstName = React.createRef();
  const refLastName = React.createRef();
  const refEmail = React.createRef();
  const refPassword = React.createRef();

  useEffect(() => {
    const tempRefFirstName = refFirstName.current;
    const tempRefLastName = refLastName.current;
    const tempRefEmail = refEmail.current;
    const tempRefPassword = refPassword.current;
    return () => {
      if (
        tempRefFirstName !== null &&
        signUpFirstName !== tempRefFirstName.value
      )
        setSignUpFirstName(tempRefFirstName.value);

      if (tempRefLastName !== null && signUpLastName !== tempRefLastName.value)
        setSignUpLastName(tempRefLastName.value);

      if (tempRefEmail !== null && signUpEmail !== tempRefEmail.value)
        setSignUpEmail(tempRefEmail.value);

      if (tempRefPassword !== null && signUpPassword !== tempRefPassword.value)
        setSignUpPassword(tempRefPassword.value);
    };

    // eslint-disable-next-line
  }, []);

  /**
   * Yup schema
   */
  const schema = yup.object({
    firstName: yup.string().required(t("First name is required")),
    lastName: yup.string().required(t("Last name is required")),
    email: yup
      .string()
      .email(t("Invalid email address"))
      .required(t("Email is required")),
    password: yup
      .string()
      .required(t("No password provided"))
      .min(8, t("Password is too short"))
      .matches(
        /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#_?&])[A-Za-z\d@$!%*#_?&]{8,}$/,
        t("Password is too weak")
      ),
  });

  /**
   * Functions
   */
  const togglePassword = () => {
    setPasswordVisible(!passwordVisible);
  };

  /**
   * Send user registration request
   * @param {object} data
   */
  const sendUserRegistrationRequest = (data) => {
    if (data === null) return false;
    data.reviser = getReviser();
    data.workplace = getWorkplace();
    data.language = language;
    data.validateEmail = true;
    return sendRequest({ req: props.request, data });
  };

  /**
   * Event listener: On Form Submit
   * @param {object} data Form data
   */
  const onSubmit = (data) => {
    // const recaptchaValue = refReCAPTCHA.current.getValue();
    // this.props.onSubmit(recaptchaValue);
    if (sendUserRegistrationRequest(data)) {
      // User registration request was submitted
    }
  };

  // const onChange = (data) => {
  //   console.log("onChange:", data);
  // };

  const UserRegistrationForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <Card
      backgroundHeader="Transparent"
      backgroundBody="Transparent"
      border="0"
      align="left"
      // maxWidth="500px"
      header={false}
    >
      <Form noValidate onSubmit={handleSubmit}>
        <h3>{t("Sign Up")}</h3>

        {/**
         * First name
         */}
        <Form.Group controlId="validationFormikFirstName">
          <Form.Label>{t("First name")}</Form.Label>
          <Form.Control
            type="text"
            placeholder={t("Enter first name")}
            name="firstName"
            ref={refFirstName}
            // autoFocus
            value={values.firstName}
            onChange={handleChange}
            isInvalid={!!errors.firstName && touched.firstName}
          />
          <Form.Control.Feedback type="invalid">
            {errors.firstName}
          </Form.Control.Feedback>
        </Form.Group>

        {/**
         * Last name
         */}
        <Form.Group controlId="validationFormikLastName">
          <Form.Label>{t("Last name")}</Form.Label>
          <Form.Control
            type="text"
            placeholder={t("Enter last name")}
            name="lastName"
            ref={refLastName}
            value={values.lastName}
            onChange={handleChange}
            isInvalid={!!errors.lastName && touched.lastName}
          />
          <Form.Control.Feedback type="invalid">
            {errors.lastName}
          </Form.Control.Feedback>
        </Form.Group>

        {/**
         * Email
         */}
        <Form.Group controlId="validationFormikEmail">
          <Form.Label>{t("Email address")}</Form.Label>
          <Form.Control
            placeholder={t("Enter email")}
            name="email"
            value={values.email}
            ref={refEmail}
            onChange={handleChange}
            isInvalid={!!errors.email && touched.email}
          />
          <Form.Control.Feedback type="invalid">
            {errors.email}
          </Form.Control.Feedback>
        </Form.Group>

        {/**
         * Password
         */}
        <Form.Group controlId="validationFormikPassword">
          <Form.Label>{t("Password")}</Form.Label>
          <InputGroup>
            <Form.Control
              type={passwordVisible ? "text" : "password"}
              placeholder={t("Enter password")}
              name="password"
              ref={refPassword}
              value={values.password}
              onChange={handleChange}
              isInvalid={!!errors.password && touched.password}
            />
            <InputGroup.Append>
              <InputGroup.Text
                id="inputGroupAppend"
                onClick={togglePassword}
                style={{
                  cursor: "pointer",
                }}
              >
                {passwordVisible ? (
                  <VisibilityOff
                    fontSize="small"
                    style={{ color: "var(--dark)" }}
                  />
                ) : (
                  <Visibility
                    fontSize="small"
                    style={{ color: "var(--dark)" }}
                  />
                )}
              </InputGroup.Text>
            </InputGroup.Append>
            <Form.Control.Feedback type="invalid">
              {errors.password}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/**
         * Submit button
         */}
        <Form.Group controlId="validationSubmitButton">
          <SubmitButton
            {...args}
            caption={t("Submit")}
            request={props.request}
            namespace={props.namespace}
          />
        </Form.Group>

        {/**
         * Login link
         */}
        <p className="other-options text-right">
          <Trans i18nKey="SignUp:Already registered">
            Already registered <a href="/sign-in">sign in?</a>
          </Trans>
        </p>
      </Form>
    </Card>
  );

  return (
    <Formik
      component={UserRegistrationForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        firstName: signUpFirstName,
        lastName: signUpLastName,
        email: signUpEmail,
        password: signUpPassword,
      }}
    />
  );
};
