/**
 * CSS
 ============================================================================ */

/**
 * System imports
 ============================================================================ */
import React from "react";
import { ListGroup, Badge } from "react-bootstrap";
import { useTranslation, Trans } from "react-i18next";
import { ArrowRightAlt } from "@material-ui/icons";

/**
  * User imports
  ============================================================================ */
import Pagination from "../pagination";
import Search from "../search";
import DateRangePicker from "../dateRangePicker";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    request: "get transactions",
    namespace: "Transactions",
  };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    transactions,
    countTransactions,
    activePageTransactions,
    transactionsPerPage,
    accountNumber,
  } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const {
    getTransactions,
    findTransactions,
    showRequestStatus,
    format,
    formatDate,
    formatPerCent,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */

  const onPageSelect = (number) => {
    // console.log("on page select", number);
    switch (number) {
      case "First":
        number = 1;
        break;
      case "Prev":
        number = activePageTransactions > 1 ? activePageTransactions - 1 : 1;
        break;
      case "Next":
        number =
          activePageTransactions <
          Math.ceil(countTransactions / transactionsPerPage)
            ? activePageTransactions + 1
            : Math.ceil(countTransactions / transactionsPerPage);
        break;
      case "Last":
        number = Math.ceil(countTransactions / transactionsPerPage);
        break;
      default:
        // number = number;
        break;
    }
    getTransactions(number);
  };

  let cnt = 0;

  /**
    * Return
    ============================================================================ */
  return (
    <>
      <div style={{ marginBottom: "1rem" }}>
        <Search
          advancedSearch={false}
          placeholder={t("Find transactions...")}
          captionAdvancedSearch={t("Advanced search")}
          onSubmit={findTransactions}
          {...args}
        />
        <br />
        <DateRangePicker
          caption={t("Period of time")}
          captionStartDate={t("Start date")}
          captionEndDate={t("End date")}
          captionUpdate={t("Update")}
          {...args}
        />
      </div>
      <ListGroup variant="flush">
        {transactions !== null &&
          transactions.map((transaction, keyTransactions) => {
            cnt++;

            const {
              // business_description,
              // business_id,
              // business_image,
              // business_link,
              // business_status,
              business_title,
              comment,
              conv_amount,
              created,
              // currency,
              // exchange_rate,
              sender_firstname,
              sender_lastname,
              recipient_firstname,
              recipient_lastname,
              // purpose_description,
              purpose_id,
              // purpose_image,
              // purpose_link,
              // purpose_status,
              purpose_title,
              recipient_id,
              roles,
              share,
              share_per_cent,
              sender_id,
              // sender_status,
              // recipient_status,
              from_account,
              to_account,
              type,
            } = transaction;

            const i_am_sender = sender_id === accountNumber;
            const i_am_recipient = recipient_id === accountNumber;
            const t_from_account = t(from_account);
            const t_to_account = t(to_account);

            const roles_available =
              roles !== null && roles !== "-" && typeof roles !== "undefined";
            const arrRoles = roles_available ? roles.split(",") : [];

            const shares_available =
              typeof share !== "undefined" && share !== null && share !== "-";

            if (type === "transfer U2S") {
              return (
                <ListGroup.Item key={keyTransactions} action>
                  <span style={{ float: "right" }}>
                    {i_am_recipient ? "" : "-"}
                    {format(conv_amount)}
                  </span>

                  <Trans
                    i18nKey="Transactions:fromAccountToAccount"
                    t_from_account={t_from_account}
                    t_to_account={t_to_account}
                  >
                    {{ t_from_account }}{" "}
                    <ArrowRightAlt style={{ color: "var(--dark)" }} />{" "}
                    {{ t_to_account }}
                  </Trans>
                  <br />
                  <small>{formatDate(created)}</small>
                </ListGroup.Item>
              );
            } else if (type === "transfer U2U") {
              return (
                <ListGroup.Item key={keyTransactions} action>
                  {i_am_recipient ? (
                    <>
                      {sender_firstname} {sender_lastname}
                    </>
                  ) : (
                    <>
                      {recipient_firstname} {recipient_lastname}
                    </>
                  )}
                  <span style={{ float: "right" }}>
                    {i_am_recipient ? "" : "-"}
                    {format(conv_amount)}
                  </span>
                  {comment !== "" && <br />}
                  {comment}
                  <br />
                  <small>{formatDate(created)}</small>
                </ListGroup.Item>
              );
            } else if (type === "transfer V2U") {
              return (
                <ListGroup.Item key={keyTransactions} action>
                  {t("Daily allowance")}
                  <span style={{ float: "right" }}>
                    {i_am_recipient ? "" : "-"}
                    {format(conv_amount)}
                  </span>
                  <br />
                  <small>{formatDate(created)}</small>
                </ListGroup.Item>
              );
            } else if (type === "transfer U2B") {
              const sender = !i_am_sender ? (
                <>
                  {sender_firstname} {sender_lastname}
                </>
              ) : (
                ""
              );
              return (
                <ListGroup.Item key={keyTransactions} action>
                  {sender}
                  <span style={{ float: "right" }}>
                    {i_am_recipient ? "" : "-"}
                    {format(conv_amount)}
                  </span>
                  {!i_am_sender && <br />}
                  {purpose_id}
                  {comment !== "" && comment !== null && (
                    <>
                      <br />
                      {comment}
                    </>
                  )}
                  <br />
                  {business_title} &middot; {purpose_title}
                  {roles_available && (
                    <>
                      <br />
                      {arrRoles.map((role, key) => {
                        return (
                          <span key={key}>
                            {" "}
                            <Badge pill variant="secondary">
                              {role}
                            </Badge>
                          </span>
                        );
                      })}
                    </>
                  )}{" "}
                  {shares_available && (
                    <>
                      <br />
                      {share} {t("shares")} ({formatPerCent(share_per_cent)})
                    </>
                  )}
                  <br />
                  <small>{formatDate(created)}</small>
                </ListGroup.Item>
              );

              // TODO: check if this works
            } else {
              console.log("Unknown transfer type:", type);

              return <></>;
            }
          })}
      </ListGroup>

      {showRequestStatus(
        "get transactions / SELECT transactions",
        cnt,
        t("Loading..."),
        t("No transactions"),
        "finished"
      )}

      <br />

      <Pagination
        {...args}
        size="sm"
        countItems={countTransactions}
        itemsPerPage={transactionsPerPage}
        active={activePageTransactions}
        onClick={onPageSelect}
      />
    </>
  );
};
