/**
 * CSS
 ============================================================================ */
// import "react-bootstrap-table-next/dist/react-bootstrap-table2.min.css";

/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
import { Form, InputGroup } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";
// import BootstrapTable from "react-bootstrap-table-next";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";
import SubmitButton from "../submitButton";
// import Pagination from "../pagination";

/**
 * Default export
 */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "CurrencyRegistration",
    request: "register currency",
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const {
    currencyCode,
    currencyName,
    currencyRegion,
    currencyNewRate,
  } = props.barrelGetters;
  const {
    setCurrencyCode,
    setCurrencyName,
    setCurrencyRegion,
    setCurrencyNewRate,
  } = props.barrelSetters;
  const {
    getReviser,
    getWorkplace,
    sendRequest,
    formatExchangeRate,
    // getCurrencies,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Destructured vars
   ============================================================================ */
  const { t } = useTranslation(["CurrencyRegistration"]);

  const refAbbr = React.createRef();
  const refTitle = React.createRef();
  const refRegion = React.createRef();
  const refRate = React.createRef();

  useEffect(() => {
    const tempRefAbbr = refAbbr.current;
    const tempRefTitle = refTitle.current;
    const tempRefRegion = refRegion.current;
    const tempRefRate = refRate.current;
    return () => {
      if (tempRefAbbr !== null && currencyCode !== tempRefAbbr.value)
        setCurrencyCode(tempRefAbbr.value);

      if (tempRefTitle !== null && currencyName !== tempRefTitle.value)
        setCurrencyName(tempRefTitle.value);

      if (tempRefRegion !== null && currencyRegion !== tempRefRegion.value)
        setCurrencyRegion(tempRefRegion.value);

      if (tempRefRate !== null && currencyNewRate !== tempRefRate.value)
        setCurrencyNewRate(tempRefRate.value);
    };

    // eslint-disable-next-line
  }, []);

  /** 
   * Yup schema
   ============================================================================ */
  const schema = yup.object({
    curr_abbr: yup
      .string()
      .required(t("Currency abbreviation is required"))
      .min(3, t("Currency abbreviation is too short"))
      .max(5, t("Currency abbreviation is too long")),
    curr_title: yup
      .string()
      .required(t("Currency name is required"))
      .min(3, t("Currency name is too short")) // must be at least 3 characters
      .max(70, t("Currency name is too long")), // must be at most 70 characters
    curr_region: yup
      .string()
      .required(t("Currency region is required"))
      .min(3, t("Currency region is too short")) // must be at least 3 characters
      .max(70, t("Currency region is too long")), // must be at most 70 characters
    curr_rate: yup
      .string()
      .required(t("Exchange rate is required"))
      .matches(
        new RegExp(t("Exchange Rate Regex")),
        t("Wrong exchange rate format")
      ),
  });

  /**
   * Functions
   ============================================================================ */
  const onSubmit = (data) => {
    // console.log(data);

    data.reviser = getReviser();
    data.workplace = getWorkplace();
    return sendRequest({ req: props.request, data });
  };

  // const indication = () => {
  //   return "THE TABLE IS EMPTY";
  // };

  /**
   * Form
   ============================================================================ */
  const CurrencyRegistrationForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <>
      <Form noValidate onSubmit={handleSubmit}>
        {/* Currency abbreviation */}
        <Form.Group>
          <Form.Label>{t("Abbreviation")}</Form.Label>
          <InputGroup>
            <Form.Control
              type="string"
              placeholder={""}
              aria-describedby="inputGroupPrepend"
              name="curr_abbr"
              ref={refAbbr}
              value={values.curr_abbr}
              onChange={handleChange}
              isInvalid={!!errors.curr_abbr && touched.curr_abbr}
            />
            <Form.Control.Feedback type="invalid">
              {errors.curr_abbr}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/* Currency name */}
        <Form.Group>
          <Form.Label>{t("Currency Name")}</Form.Label>
          <InputGroup>
            <Form.Control
              type="string"
              placeholder={""}
              aria-describedby="inputGroupPrepend"
              name="curr_title"
              ref={refTitle}
              value={values.curr_title}
              onChange={handleChange}
              isInvalid={!!errors.curr_title && touched.curr_title}
            />
            <Form.Control.Feedback type="invalid">
              {errors.curr_title}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/* Currency region */}
        <Form.Group>
          <Form.Label>{t("Currency Region")}</Form.Label>
          <InputGroup>
            <Form.Control
              type="string"
              placeholder={""}
              aria-describedby="inputGroupPrepend"
              name="curr_region"
              ref={refRegion}
              value={values.curr_region}
              onChange={handleChange}
              isInvalid={!!errors.curr_region && touched.curr_region}
            />
            <Form.Control.Feedback type="invalid">
              {errors.curr_region}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/* Initial exchange rate */}
        <Form.Group>
          <Form.Label>{t("Initial Exchange Rate")}</Form.Label>
          <InputGroup>
            <Form.Control
              type="string"
              placeholder={formatExchangeRate(1)}
              aria-describedby="inputGroupPrepend"
              name="curr_rate"
              value={values.curr_rate}
              ref={refRate}
              onChange={handleChange}
              isInvalid={!!errors.curr_rate && touched.curr_rate}
            />
            <Form.Control.Feedback type="invalid">
              {errors.curr_rate}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/* Submit button */}

        <SubmitButton
          {...args}
          caption={t("Submit")}
          request={props.request}
          namespace={props.namespace}
        />
      </Form>
    </>
  );

  /**
   * Formik return
   ============================================================================ */
  return (
    <Formik
      component={CurrencyRegistrationForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        curr_abbr: currencyCode,
        curr_title: currencyName,
        curr_region: currencyRegion,
        curr_rate: currencyNewRate,
      }}
    />
  );
};
