import React from "react";
import { useTranslation } from "react-i18next";
import { Build } from "@material-ui/icons";

export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "UnderConstruction" };

  /**
      * Properties
      ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  return (
    <>
      <h3>
        <Build style={{ color: "var(--dark)", fontSize: "3rem" }} />
        <br />
        <br />
        {t("We are constructing")}
        <br />
        <br />
      </h3>
    </>
  );
};
