/**
 * User CSS
 ============================================================================ */
import "./Article.css";

/**
 * System imports
 ============================================================================ */
import React, { lazy, useEffect, useLayoutEffect, memo } from "react";
import { useTranslation } from "react-i18next";
import {
  Accordion,
  Tab,
  ListGroup,
  Form,
  Container,
  Row,
  Col,
} from "react-bootstrap";
import ErrorBoundary from "../errorBoundary";

/**
 * User imports
 ============================================================================ */
import Card from "../card";
import ListGroupItem from "../listGroupItem";
import Breadcrumb from "../breadcrumb";
import SubmitButton from "../submitButton";
import Search from "../search";
import { useStickyState } from "../../utils/state";
import { createRef } from "react";
import HelpMenuLevel2 from "../helpMenuLevel2";

/**
 * Default export
 ============================================================================ */
const Article = (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "Article", searchable: false };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const {
    language,
    articleFeedbackSubmitted,
    scrollPositionY,
    helpPath1,
    helpPath2,
    helpPath3,
  } = props.barrelGetters;
  const {
    setArticleFeedbackSubmitted,
    setScrollPositionY,
    setHelpPath2,
  } = props.barrelSetters;
  const {
    getFileNameFromURI,
    getScrollPosition,
    setScrollPosition,
    setHelpPath,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const refFeedbackSubmit = React.createRef();
  const refFeedbackThanks = React.createRef();

  // useEffect(() => {
  //   setScrollPosition(0, scrollPositionY);
  // }, []);

  // useLayoutEffect(() => {
  //   setScrollPosition(0, scrollPositionY);
  //   console.log("SET scrollPositionY:", scrollPositionY);
  // });

  const navigation = [
    {
      name: t("Help Articles"),
      link: "#helpArticles",
    },
    {
      name: t("Community Posts"),
      link: "#communityPosts",
    },
  ];

  /**
   * Functions
   ============================================================================ */
  const getJSX = () => {
    let leadFile = getFileNameFromURI(); // "i-forgot-my-password-how-do-i-reset-it.js";

    let ExternalFile = null;

    ExternalFile =
      leadFile !== null
        ? lazy(() => {
            const ret = import(
              `../locale/${language}/articles/${leadFile}`
            ).catch((err) => {
              // console.log("err:", err);
              return import(
                `../locale/${language}/articles/i-forgot-my-password-how-do-i-reset-it.js`
              ); // TODO: Change LeadLogiIn.js -> _LeadDefault.js
            });
            // console.log("RET:", ret);

            // ret.then((value) => {
            //   console.log("THEN:", value);
            //   // expected output: "Success!"
            //   setScrollPosition(0, scrollPositionY);
            // });

            return ret;
          })
        : null;

    // console.log(refFeedbackThanks);

    const topicPath = "/help/topic";

    return (
      <Card title={t("Help center")} border={0} maxWidthBody="100%">
        <Container fluid>
          {/**
           * Search
           */}
          {props.searchable && (
            <Row>
              <Col>
                <div className="header-text">
                  <h1>{t("How can we help?")}</h1>
                </div>
                <div style={{ textAlign: "center", margin: "0 0 35px 0" }}>
                  <div
                    style={{
                      display: "inline-block",
                      width: "100%",
                      maxWidth: "600px",
                    }}
                  >
                    <Search
                      {...args}
                      placeholder={t("Ask a question or search by keyword")}
                    />
                  </div>
                </div>
              </Col>
            </Row>
          )}

          {/**
           * Help categories
           */}
          <Row>
            <Col xl={4} style={{}}>
              <HelpMenuLevel2 {...args} />
            </Col>
            <Col xl={8} style={{}}>
              <Breadcrumb
                names={[t(helpPath1), t(helpPath2), t(helpPath3)]}
                links={[
                  `${topicPath}/${helpPath1}`,
                  `${topicPath}/${helpPath2}`,
                  `${topicPath}/${helpPath2}#${helpPath3}`,
                ]}
              />

              {ExternalFile !== null && (
                <ErrorBoundary>
                  <ExternalFile {...args} />
                </ErrorBoundary>
              )}

              {/**
               * Did this help?
               */}
              <hr />

              <div ref={refFeedbackSubmit} style={{ display: "block" }}>
                <p className="feedback-heading">{t("Did this help?")}</p>
                <div
                  style={{
                    display: "inline-block",
                    maxWidth: "50px",
                    marginRight: "10px",
                  }}
                >
                  <SubmitButton
                    {...args}
                    caption={t("Yes")}
                    request="did this help"
                    namespace={props.namespace}
                    classButton="feedback-button"
                    type="button"
                    onSubmit={() => {
                      refFeedbackSubmit.current.style.display = "none";
                      refFeedbackThanks.current.style.display = "block";
                    }}
                  />
                </div>
                <div style={{ display: "inline-block", maxWidth: "50px" }}>
                  <SubmitButton
                    {...args}
                    caption={t("No")}
                    request="did this help"
                    namespace={props.namespace}
                    classButton="feedback-button"
                    type="button"
                    onSubmit={() => {
                      refFeedbackSubmit.current.style.display = "none";
                      refFeedbackThanks.current.style.display = "block";
                    }}
                  />
                </div>
              </div>

              <div ref={refFeedbackThanks} style={{ display: "none" }}>
                <p className="feedback-heading">
                  {t("Thank you for your feedback.")}
                </p>
              </div>

              {/**
               * Related Topics
               */}
              <Card
                title={t("Related topics")}
                navigation={navigation}
                headerPadding="20px 0 10px 0"
                bodyPadding="20px 0 10px 0"
                border={0}
                maxWidthBody="100%"
                cardTitleClass="related-topics-heading"
              >
                <p>
                  <a href="/help/article/i-forgot-my-password">
                    I forgot my password. How do I reset it?
                  </a>
                </p>
              </Card>
            </Col>
          </Row>
        </Container>
      </Card>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};

export default Article;
