/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
import { Form, InputGroup } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";

/**
  * User imports
  ============================================================================ */
import SubmitButton from "../submitButton";
import Card from "../card";
import Rating from "../rating";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Contact", request: "send message" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    language,
    accountNumber,
    contactSubject,
    contactMessage,
  } = props.barrelGetters;
  const { setContactSubject, setContactMessage } = props.barrelSetters;
  const { sendRequest } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]); // adjust namespace

  const refSubject = React.createRef();
  const refMessage = React.createRef();

  useEffect(() => {
    const tempRefSubject = refSubject.current;
    const tempRefMessage = refMessage.current;
    return () => {
      // console.log("ON UNMOUNT:", tempRefSubject.value);
      if (tempRefSubject !== null && contactSubject !== tempRefSubject.value)
        setContactSubject(tempRefSubject.value);

      if (tempRefMessage !== null && contactMessage !== tempRefMessage.value)
        setContactMessage(tempRefMessage.value);
      // console.log("CONTACT UNMOUNTS");
    };

    // eslint-disable-next-line
  }, []);

  /** 
    * Yup schema
    ============================================================================ */
  const schema = yup.object({
    subject: yup.string().required(t("Subject is required")),
  });

  /**
    * Functions
    ============================================================================ */
  const onSubmit = (data) => {
    data.sender = accountNumber;
    data.language = language;
    sendRequest({ req: props.request, data: data });
  };

  // const navigation = [
  //   {
  //     name: t("U2S"),
  //     link: "#u2s",
  //   },
  // ];

  /**
    * Form
    ============================================================================ */
  const ComponentForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <Card header={false} border={0} maxWidthBody="700px">
      <Form noValidate onSubmit={handleSubmit}>
        {/*
         * Rating
         */}
        <div style={{ marginBottom: "10px" }}>
          Thank you for your feedback! How helpful was our site?*
        </div>
        <div style={{ marginBottom: "10px" }}>
          <Rating {...args} />
        </div>

        {/*
         * Subject
         */}
        <Form.Group controlId="validationFormikSubject">
          <Form.Label>{t("Subject")}</Form.Label>
          <InputGroup>
            <Form.Control
              type="string"
              placeholder={t("Enter message subject")}
              aria-describedby="inputGroupPrepend"
              name="subject"
              ref={refSubject}
              value={values.subject}
              onChange={(e) => {
                handleChange(e);
                // setContactSubject(e.target.value);
              }}
              isInvalid={!!errors.subject && touched.subject}
            />
            <Form.Control.Feedback type="invalid">
              {errors.subject}
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        {/*
         * Message
         */}
        <Form.Group controlId="validationFormikMessage">
          <Form.Label>{t("Message")}</Form.Label>
          <Form.Control
            placeholder={t("Enter message")}
            name="message"
            value={values.message}
            ref={refMessage}
            onChange={handleChange}
            as="textarea"
            rows="10"
          />
        </Form.Group>

        <SubmitButton
          {...args}
          caption={t("Submit")}
          request={props.request}
          namespace={props.namespace}
        />
      </Form>
    </Card>
  );

  /**
    * Formik return
    ============================================================================ */
  return (
    <Formik
      component={ComponentForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        subject: contactSubject,
        message: contactMessage,
      }}
    />
  );
};
