/**
 * CSS
 ============================================================================ */
import "./Loading.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { Container, Row, Col } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */

export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Loading" };

  /**
        * Properties
        ============================================================================ */
  const props = Object.assign(defaults, args);

  //   const barrels = DispatcherSkeleton();

  //
  return (
    <>
      <Container fluid>
        <Row>
          {/** Left column */}
          <Col xl={4} lg={6} className="d-none d-lg-block" style={{}}>
            {/** Left column */}
          </Col>

          {/** Middle column */}
          <Col
            lg={6}
            md={12}
            style={{
              padding: 0,
              backgroundColor: "Transparent",
              textAlign: "center",
              paddingTop: "3rem",
            }}
          >
            <div className="lds-ellipsis">
              <div></div>
              <div></div>
              <div></div>
              <div></div>
            </div>
          </Col>

          {/** Right column */}
          <Col
            className="d-none d-xl-block"
            style={{ paddingLeft: 0, paddingRight: 0 }}
          >
            {/** Right column */}
          </Col>
        </Row>
      </Container>
    </>
  );
};
