/**
 * System imports
 ============================================================================ */
import React from "react";
// import _ from "lodash";
// import { useTranslation } from "react-i18next";
// import { Redirect } from "react-router-dom";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useLocation,
  // Redirect,
} from "react-router-dom";
import {
  Navbar,
  Nav,
  Container,
  Row,
  Col,
  // Button,
  // Breadcrumb,
  // NavDropdown,
  // Alert,
} from "react-bootstrap";
// import Octicon, { SignOut } from "@primer/octicons-react";

/**
  * User imports
  ============================================================================ */
// import settings from "../../../settings.json";
import AccountRegistration from "../accountRegistration";
// import BusinessRegistration from "../../businessRegistration";
import Balance from "../accountContainer";
import { Transfer } from "../transfer";
import MyBusiness from "../myBusiness";
import UserEditor from "../userEditor";
import CurrencyContainer from "../currencyContainer";
// import Login from "../../login";
import Contact from "../contact";
import Card from "../card";
import Status_404 from "../status_404";
import Help from "../help";
// import LanguageSelector from "../language-selector";
// import MainMenu from "../mainMenu";

/**
 * Component: Summary
 */
export default (args) => {
  /**
   * Default Properties
   */
  let defaults = { namespace: "Summary" };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   */
  const { settings, accountNumber } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getBusinesses } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  // const { t } = useTranslation(props.namespace);

  // const location = useLocation();

  const pageBody = (
    <Router>
      <Switch>
        <Route
          exact
          path="/"
          component={() => {
            return accountNumber !== null
              ? Balance(args)
              : AccountRegistration(args);
          }}
        />
        <Route
          exact
          path="/summary"
          component={() => {
            return accountNumber !== null
              ? Balance(args)
              : AccountRegistration(args);
          }}
        />
        <Route
          exact
          path="/account-registration"
          component={() => {
            return AccountRegistration(args);
          }}
        />
        <Route
          exact
          path="/profile"
          component={() => {
            return UserEditor({ ...args, mode: "profile" });
          }}
        />
        {settings !== null &&
          typeof settings["currencies"] !== "undefined" &&
          (settings["currencies"] === null ||
            settings["currencies"].length === 0 ||
            settings["currencies"].length > 1) && (
            <Route
              exact
              path="/currency"
              component={() => {
                return CurrencyContainer({ ...args });
              }}
            />
          )}

        <Route
          exact
          path="/my-business"
          component={() => {
            return MyBusiness(args);
          }}
        />
        <Route
          exact
          path="/balance"
          component={() => {
            return Balance(args);
          }}
        />
        <Route
          path="/transfer"
          component={() => {
            return Transfer(args);
          }}
        />

        <Route
          exact
          path="/feedback"
          component={() => {
            return Contact(args);
          }}
        />

        {/**
         * Default route
         */}
        <Route
          path="*"
          component={() => {
            return Status_404(args);
          }}
        />
      </Switch>
    </Router>
  );

  return (
    <>
      {/* Header */}

      {/* Content */}
      {/*<h3>{t("Summary")}</h3>*/}

      <Container fluid>
        <Row>
          <Col>
            <Card
              backgroundHeader="Transparent"
              backgroundBody="Transparent"
              border="0"
              align="left"
              maxWidthBody="700px"
              header={false}
            >
              {pageBody}
            </Card>
          </Col>
        </Row>
      </Container>
    </>
  );
};
