import "./Card.css";
import React, { cloneElement } from "react";
import Card from "react-bootstrap/Card";
import Nav from "react-bootstrap/Nav";
// import { Container, Row, Col, Button } from "react-bootstrap";
// import Button from "react-bootstrap/Button";

export default (args) => {
  let defaults = {
    title: "Untitled",
    navigation: undefined,
    footer: undefined,
    header: true,
    body: true,
    headerPadding: "20px",
    bodyPadding: "20px",
    align: "left",
    maxWidth: "100%",
    minWidthBody: "150px",
    maxWidthBody: "500px",
    cardTitleClass: "",
    headerContent: null,
    transparent: false,
    background: "Transparent",
    backgroundHeader: "var(--light)",
    backgroundBody: "var(--light)",
    border: "1px solid rgba(0,0,0,.125)",
    headerBorderBottom: "1px solid rgba(0,0,0,.125)",
    onNavigationClick: () => {},
  };
  let props = Object.assign(defaults, args);

  let key = 0;

  let footer = props.footer ? <Card.Footer>{props.footer}</Card.Footer> : "";

  return (
    <Card
      className="custom-card"
      style={{ background: props.background, border: props.border }}
    >
      {/* <Card.Img variant="top" src="holder.js/100px180?text=Image cap" /> */}
      {props.header && (
        <Card.Header
          className="custom-card"
          style={{
            background: props.backgroundHeader,
            borderBottom: props.headerBorderBottom,
            padding: props.headerPadding,
          }}
        >
          {props.headerContent !== null ? (
            props.headerContent
          ) : (
            <>
              {props.title.length > 0 && (
                <Card.Title className={props.cardTitleClass}>
                  {props.title}
                </Card.Title>
              )}
              <Nav
                variant="tabs"
                defaultActiveKey={props.navigationdefaultActiveKey}
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  flexWrap: "wrap",
                }}
              >
                {props.navigation &&
                  props.navigation.map((item, key) => (
                    <Nav.Item key={key}>
                      <Nav.Link
                        className="custom-card"
                        href={item.link}
                        onClick={() => props.onNavigationClick(item.link)}
                        style={{ color: "var(--dark)" }}
                      >
                        {item.name}
                      </Nav.Link>
                    </Nav.Item>
                  ))}
              </Nav>
            </>
          )}
        </Card.Header>
      )}

      {props.body && (
        <Card.Body
          className="custom-card"
          style={{
            background: props.backgroundBody,
            textAlign: props.align,
            maxWidth: props.maxWidth,
            padding: props.bodyPadding,
          }}
        >
          <div
            style={{
              minWidth: props.minWidthBody,
              maxWidth: props.maxWidthBody,
              margin: "auto",
            }}
          >
            {React.Children.map(args.children, (child) => {
              if (typeof child !== "object") {
                return child;
              } else {
                key++;
                return cloneElement(child, {
                  styles: null,
                  key: key,
                });
              }
            })}
          </div>
        </Card.Body>
      )}
      {footer}
    </Card>
  );
};
