/**
 * CSS
 ============================================================================ */
import "./AlertContainer.css";

/**
  * System imports
  ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";

/**
   * User imports
   ============================================================================ */

/**
 * Default export
 */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = { namespace: "AlertContainer", visible: false };

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
     * Destructured vars
     ============================================================================ */
  // const { t } = useTranslation([props.namespace]);

  /**
     * Functions
     ============================================================================ */
  const getJSX = () => {
    if (props.visible) {
      return (
        <div className="alert-container">
          {React.Children.map(args.children, (child) => child)}
        </div>
      );
    } else {
      return <></>;
    }
  };

  /**
     * Return
     ============================================================================ */
  return <>{getJSX()}</>;
};
