/**
 * CSS
 ============================================================================ */
import "./Breadcrumb.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Breadcrumb } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "Breadcrumb", names: "", links: "" }; // TODO: adjust namespace

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  // const arrItems = props.items.split("|");
  // const arrItems = props.items;

  /**
   * Functions
   ============================================================================ */
  const getJSX = () => {
    let key = 0;
    return (
      <Breadcrumb style={{}}>
        {props.names.map((name, index) => {
          key++;
          return name !== "" ? (
            <Breadcrumb.Item key={key} href={props.links[index]}>
              {name}
            </Breadcrumb.Item>
          ) : (
            ""
          );
        })}
      </Breadcrumb>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
