/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Accordion, Card } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
// import settings from "../../settings.json";
// import Card from "../card";
import Currencies from "../currencies";
import CurrencyRegistration from "../currencyRegistration";
import AccordionCard from "../accordionCard";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "CurrencyContainer", paddingBody: "20px 0 0 0" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    settings,
    accordionViewCurrency,
    activePageCurrencies,
    accountNumber,
  } = props.barrelGetters;
  const {
    setAccordionViewCurrency,
    setRedirectTo,
    setResponseProps,
  } = props.barrelSetters;
  const { getCurrencies } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */
  const ToggleCurrencies = () => {
    if (accordionViewCurrency !== "currencies") {
      setResponseProps(null);
      setAccordionViewCurrency("currencies");
      getCurrencies(activePageCurrencies);
    } else {
      setAccordionViewCurrency(null);
    }
  };

  const ToggleCurrencyRegistration = () => {
    setAccordionViewCurrency(
      accordionViewCurrency !== "currencyRegistration"
        ? "currencyRegistration"
        : null
    );
  };

  if (accountNumber === null) setRedirectTo("/sign-out");

  /**
    * Return
    ============================================================================ */
  if (
    typeof settings["currencies"] !== "undefined" &&
    settings["currencies"] !== null &&
    settings["currencies"].length < 2
  ) {
    return <></>; // currency creation is disabled
  } else {
    return (
      <>
        <Accordion defaultActiveKey={accordionViewCurrency}>
          {/**
           * Currencies
           */}
          <AccordionCard
            title={t("Currencies")}
            eventkey="currencies"
            active={accordionViewCurrency}
            paddingBody={props.paddingBody}
            onClick={ToggleCurrencies}
          >
            <Currencies {...args} />
          </AccordionCard>

          {/**
           * Currency registration
           */}
          {typeof settings["currencyCreationAllowed"] !== "undefined" &&
            settings["currencyCreationAllowed"] && (
              <AccordionCard
                title={t("Currency Registration")}
                eventkey="currencyRegistration"
                active={accordionViewCurrency}
                paddingBody={props.paddingBody}
                onClick={ToggleCurrencyRegistration}
              >
                <CurrencyRegistration {...args} />
              </AccordionCard>
            )}
        </Accordion>
      </>
    );
  }
};
