import "./DatePicker.css";

/**
 * System imports
 ============================================================================ */
import React, { useState } from "react";
// import { Form, InputGroup } from "react-bootstrap";
// import { Formik } from "formik";
// import * as yup from "yup";
import { useTranslation } from "react-i18next";
// import Flatpickr from "react-flatpickr";
// eslint-disable-next-line
// import { Latvian } from "flatpickr/dist/l10n/lv.js";
// import { DateRange } from "@material-ui/icons";

/**
   * User imports
   ============================================================================ */
// import SubmitButton from "../submitButton";
// import Card from "../card";
import Flatpickr from "./DatePickerWrapper";

/**
 * Default export
 */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = {
    namespace: "DatePicker",
    request: "get something",
    value: "",
    onChange: () => {},
    placeholder: "Date",
  }; // TODO: Adjust this

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  const { language } = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
     * Destructured vars
     ============================================================================ */
  const { t } = useTranslation([props.namespace]); // adjust namespace

  // const [placeholder, setPlaceholder] = useState(props.placeholder);

  /**
    * Create refs
    ============================================================================ */
  // const refFlatpickr = React.createRef();

  // Flatpickr(myElem, {
  //   locale: Latvian, // locale for this instance only
  // });

  // Flatpickr.localize(Latvian);
  // Flatpickr.localize(Flatpickr.l10ns.lv);
  // flatpickr("mySelector");

  /** 
     * Yup schema
     ============================================================================ */
  // const schema = yup.object({
  //   dateStart: yup.string().required(t("Start date is required")),
  // });

  /**
     * Functions
     ============================================================================ */
  // const onSubmit = (data) => {
  //   console.log(data);
  // };

  /**
     * Return
     ============================================================================ */
  return (
    <Flatpickr
      placeholder={props.placeholder}
      value={props.value}
      options={{
        locale: language,
        showMonths: 1,
        inline: false,
        shorthandCurrentMonth: false,
        allowInput: true,
        clickOpens: false,
        monthSelectorType: "static",
        altInput: true,
        altFormat: t("Y-m-d"),
        dateFormat: "Y-m-d", // "d.m.Y",
        // dateFormat: "Y-m-d",
        // minDate: new Date().fp_incr(-7),
        maxDate: "today",
        // mode: "range",
        defaultDate: props.value,
        onChange: function (selectedDates, dateStr, instance) {
          console.log(selectedDates, dateStr, instance);
          props.onChange(dateStr);
        },
      }}
    />
  );
};
