/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { ListGroup } from "react-bootstrap";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {
    namespace: "ListGroupItem",
    caption: "Untitled",
    key: "untitled",
    href: "#",
    onClick: () => {},
  };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  // console.log("props:", props);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */
  const getJSX = () => {
    return (
      <ListGroup.Item
        action
        key={props.key}
        eventKey={props.key}
        style={{ cursor: "default", overflow: "hidden" }}
        href={props.href}
        onClick={props.onClick}
      >
        {props.caption}
      </ListGroup.Item>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
