/**
 * System CSS
 ============================================================================ */
// import "react-calendar/dist/Calendar.css";

/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
import { Form, InputGroup, Col, Button } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";
import { Search as SearchIcon, Replay as ReplayIcon } from "@material-ui/icons";
// import Autosuggest from "react-autosuggest";
// import Calendar from "react-calendar";

/**
   * User imports
   ============================================================================ */
// import SubmitButton from "../submitButton";
// import Card from "../card";
// import { useStickyState } from "../../utils/state";
import DatePicker from "../datePicker";
// import SubmitButton from "../submitButton";

/**
 * Default export
 */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = {
    namespace: "DateRangePicker",
    request: "find currencies",
    caption: "Period of time",
    captionStartDate: "Start date",
    captionEndDate: "End date",
    captionUpdate: "Update",
    onSubmit: () => {},
  }; // TODO: Adjust this

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  const { search, dateRangeStart, dateRangeEnd } = props.barrelGetters;
  const { setSearch, setDateRangeStart, setDateRangeEnd } = props.barrelSetters;
  const {
    getTransactions,
    formatDate,
    clearRequestStatus,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
     * Destructured vars
     ============================================================================ */
  const { t } = useTranslation([props.namespace]); // adjust namespace

  /** 
     * Hooks
     ============================================================================ */
  // const [suggestions, setSuggestions] = useStickyState([], "suggestions");

  /**
     * Create refs
     ============================================================================ */
  const refSearch = React.createRef();
  // const refDateRangeStart = React.createRef();
  // const refDateRangeEnd = React.createRef();

  // useEffect(() => {
  //   const tempRefDateRangeStart = refDateRangeStart.current;
  //   const tempRefDateRangeEnd = refDateRangeEnd.current;
  //   return () => {
  //     if (
  //       tempRefDateRangeStart !== null &&
  //       dateRangeStart !== tempRefDateRangeStart.value
  //     )
  //       setDateRangeStart(tempRefDateRangeStart.value);

  //     if (
  //       tempRefDateRangeEnd !== null &&
  //       dateRangeEnd !== tempRefDateRangeEnd.value
  //     )
  //       setDateRangeEnd(tempRefDateRangeEnd.value);
  //   };

  //   // eslint-disable-next-line
  // }, []);

  /**
     * Clear refs
     ============================================================================ */
  useEffect(() => {
    const tempRefSearch = refSearch.current;
    return () => {
      if (tempRefSearch !== null && search !== tempRefSearch.value)
        setSearch(tempRefSearch.value);
    };

    // eslint-disable-next-line
  }, []);

  /** 
     * Yup schema
     ============================================================================ */
  const schema = yup.object({
    dateStart: yup.string().required(t("Date start is required")),
    dateEnd: yup.string().required(t("Date end is required")),
  });

  /**
     * Functions
     ============================================================================ */
  const onSubmit = () => {
    getTransactions(1);

    // findCurrencies(data.search);
    // props.onSubmit(data.search);
  };

  // const handleChange = (data) => {
  //   console.log(data);
  // };

  /**
     * Form
     ============================================================================ */
  const DateRangePickerForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <>
      <Form noValidate onSubmit={handleSubmit}>
        {/**
         * Caption: Period of time
         */}
        <Form.Row style={{ margin: "0" }}>
          <Form.Group
            as={Col}
            controlId="formGridPeriodOfTime"
            style={{ margin: "0", padding: "0 0" }}
          >
            <Form.Label>{props.caption}</Form.Label>
          </Form.Group>
        </Form.Row>

        {/**
         * Date start / Date end
         */}
        <Form.Row style={{ margin: "0" }}>
          {/**
           * Date start
           */}
          <Form.Group
            as={Col}
            style={{ margin: "0", padding: "0" }}
            controlId="validationFormikDateStart"
          >
            <DatePicker
              name="dateStart"
              value={values.dateStart}
              onChange={(data) => {
                // console.log(data);
                setDateRangeStart(data);
              }}
              isInvalid={!!errors.dateStart && touched.dateStart}
              placeholder={props.captionStartDate}
              {...args}
            />
            <Form.Control.Feedback type="invalid">
              {errors.dateStart}
            </Form.Control.Feedback>
          </Form.Group>

          {/**
           * Dash
           */}
          <span style={{ margin: "0", padding: "0.4rem 0.5rem" }}>-</span>

          {/**
           * Date end
           */}
          <Form.Group
            as={Col}
            style={{ padding: "0 0" }}
            controlId="validationFormikDateEnd"
          >
            <DatePicker
              name="dateEnd"
              value={values.dateEnd}
              onChange={(data) => {
                // console.log(data);
                setDateRangeEnd(data);
              }}
              isInvalid={!!errors.dateEnd && touched.dateEnd}
              placeholder={props.captionEndDate}
              {...args}
            />
            <Form.Control.Feedback type="invalid">
              {errors.dateEnd}
            </Form.Control.Feedback>
          </Form.Group>
        </Form.Row>

        {/**
         * Update
         */}
        <Form.Row style={{ margin: "0" }}>
          <button
            type="button"
            onClick={onSubmit}
            className="button-transparent"
            style={{ margin: "-0.5rem 0 0 0", padding: "0" }}
          >
            <ReplayIcon className="sm" style={{ color: "var(--dark)" }} />
            <span
              style={{
                margin: "0 0.5rem",
              }}
            >
              {props.captionUpdate}
            </span>
          </button>
        </Form.Row>
      </Form>
    </>
  );

  /**
     * Formik return
     ============================================================================ */
  return (
    <Formik
      component={DateRangePickerForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        dateStart: dateRangeStart,
        dateEnd: dateRangeEnd,
      }}
    />
  );
};
