/**
  * System imports
  ============================================================================ */
import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Form, InputGroup } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";

/**
  * User imports
  ============================================================================ */
import SubmitButton from "../submitButton";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {
    namespace: "PurposeRegistration",
    requestInsert: "register purpose",
    requestUpdate: "update purpose property",
    businessID: null,
    purposeID: null,
    title: "",
    description: "",
    category: "",
    subcategory: "",
    subcategory2: "",
    keywords: "",
  };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  // console.log("PURPOSE REGISTRATION PROPS:", props);

  const barrels = props;
  const { businessID, purposeID, id } = props;

  /**
   * Barrels
   */
  const {
    accountNumber,
    language,
    // businessPurposes,
    purposeRegistrationTitle,
    purposeRegistrationDescription,
    // purposeRegistrationCategory,
    // purposeRegistrationSubcategory,
    // purposeRegistrationSubcategory2,
    purposeRegistrationKeywords,
  } = barrels.barrelGetters;
  const {
    // setBusinessPurposes,
    setPurposeRegistrationTitle,
    setPurposeRegistrationDescription,
    // setPurposeRegistrationCategory,
    // setPurposeRegistrationSubcategory,
    // setPurposeRegistrationSubcategory2,
    setPurposeRegistrationKeywords,
  } = barrels.barrelSetters;
  const { getReviser, getWorkplace } = barrels.barrelFunctions;
  const { onPurposeRegistration } = barrels.barrelEventHandlers;

  // const userDataParsed = JSON.parse(userData);
  // const userDataParsed = userData;
  const { t } = useTranslation([props.namespace]);

  const refPurposeTitle = React.createRef();
  const refPurposeDescription = React.createRef();
  // const refPurposeCategory = React.createRef();
  // const refPurposeSubcategory = React.createRef();
  // const refPurposeSubcategory2 = React.createRef();
  const refPurposeKeywords = React.createRef();

  useEffect(() => {
    const tempRefPurposeTitle = refPurposeTitle.current;
    const tempRefPurposeDescription = refPurposeDescription.current;
    const tempRefPurposeKeywords = refPurposeKeywords.current;
    return () => {
      if (
        tempRefPurposeTitle !== null &&
        purposeRegistrationTitle !== tempRefPurposeTitle.value
      )
        setPurposeRegistrationTitle(tempRefPurposeTitle.value);

      if (
        tempRefPurposeDescription !== null &&
        purposeRegistrationDescription !== tempRefPurposeDescription.value
      )
        setPurposeRegistrationDescription(tempRefPurposeDescription.value);

      if (
        tempRefPurposeKeywords !== null &&
        purposeRegistrationKeywords !== tempRefPurposeKeywords.value
      )
        setPurposeRegistrationKeywords(tempRefPurposeKeywords.value);
    };

    // eslint-disable-next-line
  }, []);

  const schema = yup.object({
    purposeTitle: yup
      .string()
      .required(t("Purpose title is a required field"))
      .min(3, t("Purpose title too short"))
      .max(70, t("Purpose title too long"))
      .trim(),
    purposeDescription: yup.string().optional(),
    // purposeCategory: yup
    //   .string()
    //   .required(t("Purpose category is a required field"))
    //   .max(70, t("Purpose category too long"))
    //   .trim(),
    // purposeSubcategory: yup.string().optional(),
    // purposeSubcategory2: yup.string().optional(),
    purposeKeywords: yup.string().optional(),
  });

  /**
   * Transfer to self
   * @param {object} data
   */
  const RegisterPurpose = (data) => {
    data.reviser = getReviser();
    data.workplace = getWorkplace();
    data.businessID = businessID;
    data.id = id;
    data.purposeID = purposeID;
    data.uid = accountNumber;
    data.language = language;

    // console.log("REGISTER PURPOSE DATA:", data);

    onPurposeRegistration({
      req: props.purposeID === null ? props.requestInsert : props.requestUpdate,
      data: data,
    });
  };

  const onSubmit = (data) => {
    RegisterPurpose(data);
  };

  const RegisterPurposeForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    errors,
  }) => (
    <Form noValidate onSubmit={handleSubmit}>
      {/*
       * Purpose Title (Purpose ID)
       */}
      <Form.Group controlId="validationFormikPurposeID">
        <Form.Label>{t("Purpose Title")}</Form.Label>
        <InputGroup>
          <Form.Control
            type="text"
            placeholder=""
            aria-describedby="inputGroupPrepend"
            name="purposeTitle"
            ref={refPurposeTitle}
            value={values.purposeTitle}
            onChange={handleChange}
            isInvalid={!!errors.purposeTitle && touched.purposeTitle}
          />
          <Form.Control.Feedback type="invalid">
            {errors.purposeTitle}
          </Form.Control.Feedback>
        </InputGroup>
      </Form.Group>

      {/*
       * Purpose Description
       */}
      <Form.Group controlId="purposeDescription">
        <Form.Label>{t("Purpose Description")}</Form.Label>
        <Form.Control
          as="textarea"
          rows="3"
          name="purposeDescription"
          ref={refPurposeDescription}
          value={values.purposeDescription}
          onChange={handleChange}
        />
      </Form.Group>

      {/*
       * Keywords
       */}
      <Form.Group controlId="validationFormikPurposeKeywords">
        <Form.Label>{t("Purpose Keywords")}</Form.Label>
        <InputGroup>
          <Form.Control
            type="text"
            placeholder=""
            name="purposeKeywords"
            ref={refPurposeKeywords}
            value={values.purposeKeywords}
            onChange={handleChange}
            isInvalid={!!errors.purposeKeywords && touched.purposeKeywords}
          />
          <Form.Control.Feedback type="invalid">
            {errors.purposeKeywords}
          </Form.Control.Feedback>
        </InputGroup>
      </Form.Group>

      {/*
       * Submit button
       */}
      <SubmitButton
        {...args}
        caption={t(props.purposeID === null ? "Submit" : "Save")}
        request={
          props.purposeID === null ? props.requestInsert : props.requestUpdate
        }
        namespace={props.namespace}
      />
    </Form>
  );

  return (
    <Formik
      component={RegisterPurposeForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        purposeTitle: purposeRegistrationTitle,
        purposeDescription: purposeRegistrationDescription,
        // purposeCategory: purposeRegistrationCategory,
        // purposeSubcategory: purposeRegistrationSubcategory,
        // purposeSubcategory2: purposeRegistrationSubcategory2,
        purposeKeywords: purposeRegistrationKeywords,
      }}
    />
  );
};
