// import { useTranslation } from "react-i18next";

export default ({ args, barrels }) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {};

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { debug, error: msgError } = barrels.barrelGetters;
  const { setRequest, setReconnecting } = barrels.barrelSetters;
  const { showMessage } = barrels.barrelFunctions;
  // const {} = barrels.barrelEventHandlers;

  //   const { t } = useTranslation("Errors");
  const { req, error, context } = props;
  if (debug)
    console.log(`SYSTEM ERROR: ${req}`, error, context, props, barrels);

  let errorFound = false;

  /** 
   * Request event dispatcher
   ============================================================================ */
  const onError = (data, callback) => {
    if (errorFound) return;
    const [res, status] = data;

    if (
      (res === req && status === error.code) ||
      (res === "*" && status === error.code) ||
      res === "unknown"
    ) {
      callback();
      setRequest(null);
      errorFound = true;
      return true;
    }
    return false;
  };

  /** 
   * Response handlers
  ============================================================================ */

  // onError(["*", "INVALID_SESSION"], () => {
  //   showMessage("SESSION_TIMED_OUT", "info");
  //   setRedirectTo("/sign-out");
  //   return true;
  // });

  /**
   * Reconnect error
   */
  onError(["connect_error", "none"], () => {
    if (msgError !== "SERVER_UNAVAILABLE") {
      // showMessage("SERVER_UNAVAILABLE", "error");
      setReconnecting(true);
    }

    return true;
  });

  /**
   * Reconnect error
   */
  onError(["connect_timeout", "connect_timeout"], () => {
    return true;
  });

  /**
   * Reconnect error
   */
  onError(["reconnect_error", "TransportError"], () => {
    if (msgError !== "SERVER_STILL_UNAVAILABLE") {
      // showMessage("SERVER_STILL_UNAVAILABLE", "error");
      setReconnecting(true);
    }

    return true;
  });

  onError(["unknown"], () => {
    showMessage(error.code, "error");
    return true;
  });

  return false;
};
