/**
 * System imports
 ============================================================================ */
import React, { useState } from "react";
// import { useTranslation } from "react-i18next";
import { Button, Spinner } from "react-bootstrap";

/**
  * User imports
  ============================================================================ */
// import { useTimeout } from "../../utils/useTimeout";

/**
 * Default export
 */
// export default (args) => {
const SubmitButton = React.memo((args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    caption: "Submit",
    request: null,
    namespace: "Button",
    type: "submit",
    classButton: "",
    visible: true,
    marginTop: 0,
    timeout: 15000, // milliseconds
    variant: "primary", // primary | secondary
    onSubmit: () => {},
  };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { request } = props.barrelGetters;
  const { setError, setInfo } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  // const { t } = useTranslation([props.namespace]); // adjust namespace

  const [requestExpired] = useState(false);

  const onSubmit = (e) => {
    setError(null);
    setInfo(null);
    // document.body.style.overflow = "hidden";
    props.onSubmit();
    // e.preventDefault();
  };

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    return !props.visible ? (
      <></>
    ) : (
      <Button
        type={props.type}
        className={props.classButton}
        onClick={onSubmit}
        block
        variant={props.variant === "primary" ? "primary" : "outline-primary"}
        disabled={request === props.request && !requestExpired}
        style={{
          marginTop: props.marginTop + "px",
          whiteSpace: "nowrap",
        }}
      >
        <Spinner
          as="span"
          animation="grow"
          size="sm"
          role="status"
          aria-hidden="true"
          hidden={request !== props.request || requestExpired}
        />
        &nbsp;
        {props.caption}
        &nbsp;
      </Button>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
});

export default SubmitButton;
