import "./UserEditor.css";
import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { ListGroup, Form, InputGroup } from "react-bootstrap";
import {
  Visibility,
  VisibilityOff,
  Close,
  Edit,
  // Done,
} from "@material-ui/icons";
import { Formik } from "formik";
import * as yup from "yup";
import Card from "../card";
import SubmitButton from "../submitButton";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {
    namespace: "UserEditor",
    request: "update user",
    mode: "label",
  };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  // console.log("USER EDITOR PROPS:", props);

  /**
   * Barrels
   */
  const {
    userData,
    accountNumber,
    // currencies,
    // currencyAbbr,
    // language,
    userEditorEditFirstname,
    userEditorFirstname,
    userEditorEditLastname,
    userEditorLastname,
    userEditorEditEmail,
    userEditorEmail,
    userEditorEditPassword,
    userEditorPassword,
    userEditorPasswordVisible,
    userEditorSubmitButtonVisible,
  } = props.barrelGetters;
  const {
    setUserEditorEditFirstname,
    setUserEditorFirstname,
    setUserEditorEditLastname,
    setUserEditorLastname,
    setUserEditorEditEmail,
    setUserEditorEmail,
    setUserEditorEditPassword,
    setUserEditorPassword,
    setUserEditorPasswordVisible,
    setUserEditorSubmitButtonVisible,
  } = props.barrelSetters;
  const { sendRequest, getReviser, getWorkplace } = props.barrelFunctions;
  // const {  } = props.barrelEventHandlers;

  const { t } = useTranslation([props.namespace]);

  const refFirstName = React.createRef();
  const refLastName = React.createRef();
  const refEmail = React.createRef();
  const refPassword = React.createRef();

  useEffect(() => {
    const tempRefFirstName = refFirstName.current;
    const tempRefLastName = refLastName.current;
    const tempRefEmail = refEmail.current;
    const tempRefPassword = refPassword.current;
    return () => {
      if (
        tempRefFirstName !== null &&
        userEditorFirstname !== tempRefFirstName.value
      )
        setUserEditorFirstname(tempRefFirstName.value);

      if (
        tempRefLastName !== null &&
        userEditorLastname !== tempRefLastName.value
      )
        setUserEditorLastname(tempRefLastName.value);

      if (tempRefEmail !== null && userEditorEmail !== tempRefEmail.value)
        setUserEditorEmail(tempRefEmail.value);

      if (
        tempRefPassword !== null &&
        userEditorPassword !== tempRefPassword.value
      )
        setUserEditorPassword(tempRefPassword.value);
    };

    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (
      userEditorEditFirstname ||
      userEditorEditLastname ||
      userEditorEditEmail ||
      userEditorEditPassword
    ) {
      setUserEditorSubmitButtonVisible(true);
    } else {
      setUserEditorSubmitButtonVisible(false);
    }

    // eslint-disable-next-line
  }, [
    userEditorEditFirstname,
    userEditorEditLastname,
    userEditorEditEmail,
    userEditorEditPassword,
  ]);

  /**
   * Functions
   */

  const togglePassword = () => {
    setUserEditorPasswordVisible(!userEditorPasswordVisible);
  };

  const sendUserRegistrationRequest = (data) => {
    if (data === null) return false;
    // data.reviser = getReviser();
    // data.workplace = getWorkplace();
    // data.language = language;
    // data.email = userData.email;
    return sendRequest({ req: props.request, data });
  };

  /**
   * Event listener: On Form Submit
   * @param {object} data Form data
   */
  const onSubmit = (data) => {
    data.personalID = accountNumber;
    if (!userEditorEditFirstname) data.firstName = null;
    if (!userEditorEditLastname) data.lastName = null;
    if (!userEditorEditEmail) data.email = userData.email;
    if (!userEditorEditPassword) data.password = null;

    if (data.email !== userData.email) {
      data.newEmail = data.email;
      data.email = userData.email;
      data.validateEmail = true;
    }

    // console.log("onSubmit:", data);

    if (sendUserRegistrationRequest(data)) {
      // User registration request was submitted
    }
  };

  const yupObject = {
    firstName: userEditorEditFirstname
      ? yup.string().required(t("First name is required"))
      : yup.string().optional(),
    lastName: userEditorEditLastname
      ? yup.string().required(t("Last name is required"))
      : yup.string().optional(),
    email: userEditorEditEmail
      ? yup
          .string()
          .email(t("Invalid email address"))
          .required(t("Email is required"))
      : yup.string().optional(),
    password: userEditorEditPassword
      ? yup
          .string()
          .required(t("No password provided"))
          .min(8, t("Password is too short"))
          // .matches(/[a-zA-Z]/, "Password can only contain Latin letters.")
          .matches(
            /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#_?&])[A-Za-z\d@$!%*#_?&]{8,}$/,
            t("Password is too weak")
          )
      : yup.string().optional(),
  };

  /**
   * Yup schema
   */
  const schema = yup.object(yupObject);

  /**
   * Data consistency tests
   */
  if (userData === null) return <></>;

  /**
   * Destructuring
   */
  const { mode } = props;
  const {
    firstname,
    lastname,
    email,
    // language,
    // currency_name,
    // currency_abbr,
  } = userData;
  // const currency = `${currency_name} (${currency_abbr})`;

  const UserEditorForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <Form noValidate onSubmit={handleSubmit}>
      <Card title={t("Profile")} border={0} maxWidthBody="700px">
        {/* First name */}
        <ListGroup variant="flush">
          <ListGroup.Item as="div" action>
            <Form.Label>{t("First name")}</Form.Label>
            {!userEditorEditFirstname ? (
              <>
                <span style={{ float: "right" }}>
                  <Edit
                    onClick={() => setUserEditorEditFirstname(true)}
                    fontSize="small"
                    style={{ color: "var(--dark)", cursor: "pointer" }}
                  />
                </span>
                <br />
                <small>{firstname}</small>
              </>
            ) : (
              <span style={{ float: "right" }}>
                <Close
                  onClick={() => setUserEditorEditFirstname(false)}
                  fontSize="small"
                  style={{ color: "var(--dark)", cursor: "pointer" }}
                />
              </span>
            )}

            {userEditorEditFirstname && (
              <Form.Group controlId="validationFormikFirstName">
                <InputGroup>
                  <Form.Control
                    type="text"
                    placeholder={t("Enter first name")}
                    name="firstName"
                    ref={refFirstName}
                    value={values.firstName}
                    onChange={handleChange}
                    isInvalid={!!errors.firstName && touched.firstName}
                  />
                  <Form.Control.Feedback type="invalid">
                    {errors.firstName}
                  </Form.Control.Feedback>
                </InputGroup>
              </Form.Group>
            )}
          </ListGroup.Item>

          {/* Last name */}
          <ListGroup.Item as="div" action>
            <Form.Label>{t("Last name")}</Form.Label>
            {!userEditorEditLastname ? (
              <>
                <span style={{ float: "right" }}>
                  <Edit
                    onClick={() => setUserEditorEditLastname(true)}
                    fontSize="small"
                    style={{ color: "var(--dark)", cursor: "pointer" }}
                  />
                </span>
                <br />
                <small>{lastname}</small>
              </>
            ) : (
              <span style={{ float: "right" }}>
                <Close
                  onClick={() => setUserEditorEditLastname(false)}
                  fontSize="small"
                  style={{ color: "var(--dark)", cursor: "pointer" }}
                />
              </span>
            )}

            {userEditorEditLastname && (
              <Form.Group controlId="validationFormikLastName">
                <InputGroup>
                  <Form.Control
                    type="text"
                    placeholder={t("Enter last name")}
                    name="lastName"
                    ref={refLastName}
                    value={values.lastName}
                    onChange={handleChange}
                    isInvalid={!!errors.lastName && touched.lastName}
                  />
                  <Form.Control.Feedback type="invalid">
                    {errors.lastName}
                  </Form.Control.Feedback>
                </InputGroup>
              </Form.Group>
            )}
          </ListGroup.Item>

          {/* Email */}
          {accountNumber !== null && (
            <ListGroup.Item as="div" action>
              <Form.Label>{t("Email address")}</Form.Label>
              {!userEditorEditEmail ? (
                <>
                  <span style={{ float: "right" }}>
                    <Edit
                      onClick={() => setUserEditorEditEmail(true)}
                      fontSize="small"
                      style={{ color: "var(--dark)", cursor: "pointer" }}
                    />
                  </span>
                  <br />
                  <small>{email}</small>
                </>
              ) : (
                <span style={{ float: "right" }}>
                  <Close
                    onClick={() => setUserEditorEditEmail(false)}
                    fontSize="small"
                    style={{ color: "var(--dark)", cursor: "pointer" }}
                  />
                </span>
              )}

              {userEditorEditEmail && (
                <Form.Group controlId="validationFormikEmail">
                  <InputGroup>
                    <Form.Control
                      placeholder={t("Enter email")}
                      name="email"
                      value={values.email}
                      ref={refEmail}
                      onChange={handleChange}
                      isInvalid={!!errors.email && touched.email}
                    />
                    <Form.Control.Feedback type="invalid">
                      {errors.email}
                    </Form.Control.Feedback>
                  </InputGroup>
                </Form.Group>
              )}
            </ListGroup.Item>
          )}

          {/* Password */}
          <ListGroup.Item as="div" action>
            <Form.Label>{t("Password")}</Form.Label>
            {!userEditorEditPassword ? (
              <>
                <span style={{ float: "right" }}>
                  <Edit
                    onClick={() => setUserEditorEditPassword(true)}
                    fontSize="small"
                    style={{ color: "var(--dark)", cursor: "pointer" }}
                  />
                </span>
              </>
            ) : (
              <span style={{ float: "right" }}>
                <Close
                  onClick={() => setUserEditorEditPassword(false)}
                  fontSize="small"
                  style={{ color: "var(--dark)", cursor: "pointer" }}
                />
              </span>
            )}

            {userEditorEditPassword && (
              <>
                {/* New password */}
                <Form.Group controlId="validationFormikPassword">
                  <InputGroup>
                    <Form.Control
                      type={userEditorPasswordVisible ? "text" : "password"}
                      placeholder={t("Enter new password")}
                      name="password"
                      ref={refPassword}
                      value={values.password}
                      onChange={handleChange}
                      isInvalid={!!errors.password && touched.password}
                    />
                    <InputGroup.Append>
                      <InputGroup.Text
                        id="inputGroupAppend"
                        onClick={togglePassword}
                        style={{
                          cursor: "pointer",
                        }}
                      >
                        {userEditorPasswordVisible ? (
                          <VisibilityOff
                            fontSize="small"
                            style={{ color: "var(--dark)", cursor: "pointer" }}
                          />
                        ) : (
                          <Visibility
                            fontSize="small"
                            style={{ color: "var(--dark)", cursor: "pointer" }}
                          />
                        )}
                      </InputGroup.Text>
                    </InputGroup.Append>

                    <Form.Control.Feedback type="invalid">
                      {errors.password}
                    </Form.Control.Feedback>
                  </InputGroup>
                </Form.Group>
              </>
            )}
          </ListGroup.Item>
        </ListGroup>

        <Form.Group controlId="validationSubmitButton">
          <SubmitButton
            {...args}
            caption={t("Submit")}
            visible={userEditorSubmitButtonVisible}
            marginTop={20}
            request={props.request}
            namespace={props.namespace}
          />
        </Form.Group>
      </Card>
    </Form>
  );

  const jsx =
    mode === "label" ? (
      <span style={{ whiteSpace: "nowrap" }}>
        {firstname} {lastname}
      </span>
    ) : (
      <Formik
        component={UserEditorForm}
        validationSchema={schema}
        onSubmit={onSubmit}
        initialValues={{
          firstName: userEditorFirstname,
          lastName: userEditorLastname,
          email: userEditorEmail,
          password: userEditorPassword,
        }}
      />
    );

  return <>{jsx}</>;
};
