/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { ListGroup } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";
import ListGroupItem from "../listGroupItem";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "HelpMenuLevel2" };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const { helpPath1, helpPath2 } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { setHelpPath } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  // console.log(t("Common Questions"));

  /**
   * Functions
   ============================================================================ */
  const getJSX = () => {
    switch (helpPath1) {
      case "#business":
        return (
          <ListGroup variant="flush" defaultActiveKey={helpPath2}>
            <ListGroupItem
              {...args}
              caption={t("Common Questions")}
              key="common-questions"
              href="/help/topic/common-questions"
              onClick={() => setHelpPath(2, "common-questions")}
            />
            <ListGroupItem
              {...args}
              caption={t("Merchant Services")}
              key="merchant-services"
              href="/help/topic/merchant-services"
              onClick={() => setHelpPath(2, "merchant-services")}
            />
            <ListGroupItem
              {...args}
              caption={t("My Account")}
              key="my-account"
              href="/help/topic/my-account"
              onClick={() => setHelpPath(2, "my-account")}
            />
            <ListGroupItem
              {...args}
              caption={t("Local Currency Basics")}
              key="local-currency-basics"
              href="/help/topic/local-currency-basics"
              onClick={() => setHelpPath(2, "local-currency-basics")}
            />
          </ListGroup>
        );
        break;

      case "#technical":
        return (
          <ListGroup variant="flush" defaultActiveKey={helpPath2}>
            <ListGroupItem
              {...args}
              caption={t("Common Questions")}
              key="common-questions"
              href="/help/topic/common-questions"
              onClick={() => setHelpPath(2, "common-questions")}
            />
            <ListGroupItem
              {...args}
              caption={t("APIs")}
              key="apis"
              href="/help/topic/apis"
              onClick={() => setHelpPath(2, "apis")}
            />
            <ListGroupItem
              {...args}
              caption={t("Shopping Carts")}
              key="shopping-carts"
              href="/help/topic/shopping-carts"
              onClick={() => setHelpPath(2, "shopping-carts")}
            />
            <ListGroupItem
              {...args}
              caption={t("Products")}
              key="products"
              href="/help/topic/products"
              onClick={() => setHelpPath(2, "products")}
            />
          </ListGroup>
        );
        break;

      default:
        // #personal
        return (
          <ListGroup variant="flush" defaultActiveKey={helpPath2}>
            <ListGroupItem
              {...args}
              caption={t("Common Questions")}
              key="common-questions"
              href="/help/topic/common-questions"
              onClick={() => setHelpPath(2, "common-questions")}
            />
            <ListGroupItem
              {...args}
              caption={t("My Account")}
              key="my-account"
              href="/help/topic/my-account"
              onClick={() => setHelpPath(2, "my-account")}
            />
            <ListGroupItem
              {...args}
              caption={t("Local Currency Basics")}
              key="local-currency-basics"
              href="/help/topic/local-currency-basics"
              onClick={() => setHelpPath(2, "local-currency-basics")}
            />
          </ListGroup>
        );
        break;
    }
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
