import { useState, useEffect } from "react";
import config from "./config";
import openSocket from "socket.io-client";
// import { useTimeout } from "./useTimeout";

let socket = openSocket.connect(config.uri.ENDPOINT, {
  secure: true,
  reconnect: true,
});

/**
 * Send request to the server
 * @param {*} param0
 */
export const sendData = (data, debug = false) => {
  if (debug) console.log(`request: ${data.req}`, data);

  // No data - do not send
  // if (req.length === 0) return false;

  // useTimeout(() => {
  //   console.log("Socket timeout");
  // }, 3000);

  socket.emit(`onDataReceived`, data);
  return true;
};

// Start a session
export const startSession = (data, session, debug = false) => {
  // console.log("startSession");
  if (debug) console.log(`request: start session`, data, session);

  socket.emit(`start session`, {
    data: data,
    session: session,
    req: "start session",
  });
};

// End the session
export const endSession = (data, session, debug = false) => {
  if (debug) console.log(`request: end session`, data, session);
  socket.emit(`end session`, {
    // char: data,
    data: data,
    // uid: uid,
    session: session,
    req: "end session",
  });
};

// Custom hook: useSocket
export const useSocket = (callback) => {
  const [responseData, setResponseData] = useState(null);

  socket.on(`onDataReceived`, (data) => {
    setResponseData(data);
  });

  useEffect(() => {
    if (responseData === null) return;

    callback(responseData);
    // eslint-disable-next-line
  }, [responseData]);
};

export const useSystemSocket = (callback, instance) => {
  useEffect(() => {
    // socket.on("onDataReceived", (data) => {
    //   callback(data);
    // });

    /**
     * System error listeners
     */
    socket.on("connect_error", () => {
      callback(
        {
          status: "error",
          req: "connect_error",
          context: {},
          error: { code: "none" },
        },
        true
      );
    });
    socket.on("connect_timeout", (timeout) => {
      callback(
        {
          status: "error",
          req: "connect_timeout",
          context: {},
          error: { code: "connect_timeout", timeout },
        },
        true
      );
    });
    socket.on("reconnect_error", (error) => {
      callback(
        {
          status: "error",
          req: "reconnect_error",
          context: {},
          error: { code: error.type, error },
        },
        true
      );
    });
    socket.on("reconnect_failed", () => {
      // This should never occur
      callback(
        {
          status: "error",
          req: "reconnect_failed",
          context: {},
          error: { code: "reconnect_failed" },
        },
        true
      );
    });
    socket.on("error", (error) => {
      callback(
        {
          status: "error",
          req: "error",
          context: {},
          error: { code: "error", error },
        },
        true
      );
    });

    /**
     * System status listeners
     */
    socket.on("ping", () => {
      // callback({ data: "ping" }, true);
    });
    socket.on("pong", (latency) => {
      // callback({ data: "pong", latency }, true);
    });

    socket.on("disconnect", (reason) => {
      if (reason === "io server disconnect") {
        // the disconnection was initiated by the server, you need to reconnect manually
        socket.connect();
      } else {
        // else the socket will automatically try to reconnect
      }
      // Reasons:
      // - "transport close": Server was stopped
      callback({ req: "disconnect", status: "success", reason }, true);
    });
    socket.on("reconnect", (attemptNumber) => {
      // Successfully reconnected on attempt: attemptNumber
      callback({ req: "reconnect", status: "success", attemptNumber }, true);
    });
    socket.on("reconnect_attempt", (attemptNumber) => {
      callback(
        { req: "reconnect attempt", status: "continue", attemptNumber },
        true
      );
    });
    socket.on("reconnecting", (attemptNumber) => {
      callback(
        { req: "reconnecting", status: "continue", attemptNumber },
        true
      );
    });
  }, [instance]);
};
