/**
 * CSS
 ============================================================================ */
import "./Leads.css";

/**
 * System imports
 ============================================================================ */
import React, { useEffect, lazy } from "react";
import { useTranslation } from "react-i18next";
import ErrorBoundary from "../errorBoundary";

/**
    * User imports
    ============================================================================ */
import Alert from "../alert";
import LanguageSelector from "../language-selector";

/**
 * Default export
 */
export default (args) => {
  /**
      * Default Properties
      ============================================================================ */
  let defaults = { namespace: "Leads" };

  /**
      * Properties
      ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
      * Barrels
      ============================================================================ */
  const { signedIn, language } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getContainerClass } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
      * Destructured vars
      ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const { wrapperClass, innerClass } = getContainerClass(signedIn);

  const appClass = signedIn ? "App" : "App bg";
  // const appClass = "App";

  /**
      * Functions
      ============================================================================ */
  const getJSX = () => {
    let leadFile = "LeadLogIn.js";
    switch (window.location.pathname) {
      case "/":
      case "/sign-in":
        leadFile = "LeadLogIn.js";
        break;

      case "/sign-up":
        leadFile = "LeadSignUp.js";
        break;

      case "/auth":
        leadFile = "LeadAuth.js";
        break;

      case "/transfer":
        leadFile = "Transfer.js";
        break;

      // case "/transfer/u2s":
      //   leadFile = "TransferU2S.js";
      //   break;

      // case "/transfer/u2u":
      //   leadFile = "TransferU2U.js";
      //   break;

      // case "/transfer/u2b":
      //   leadFile = "TransferU2B.js";
      //   break;

      default:
        leadFile = "LeadLogIn.js";
        break;
    }

    let ExternalFile = null;

    ExternalFile =
      leadFile !== null
        ? lazy(() =>
            import(`../locale/${language}/leads/${leadFile}`).catch((err) => {
              // console.log("err:", err);
              return import(`../locale/${language}/leads/LeadLogIn.js`); // TODO: Change LeadLogiIn.js -> _LeadDefault.js
            })
          )
        : null;

    return (
      <div className={appClass} style={{ padding: "0 0% 0 0%" }}>
        {/**
         * Wrapper for the main window
         */}
        <div className={wrapperClass} style={{}}>
          {/**
           * Top margin of the main window (only for >= sm)
           */}
          <div className="d-none d-md-block" style={{ height: "1rem" }}></div>

          {/**
           * The main window
           */}
          <div
            className=""
            style={{
              background: "Transparent",
              boxShadow: "none",
              padding: "0 15px",
            }}
          >
            {ExternalFile !== null && (
              <ErrorBoundary>
                <ExternalFile {...args} />
              </ErrorBoundary>
            )}
          </div>
        </div>
      </div>
    );
  };

  /**
      * Return
      ============================================================================ */
  return <>{getJSX()}</>;
};
