/**
 * System CSS
 ============================================================================ */
// import "react-calendar/dist/Calendar.css";

/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
import { Form, InputGroup, Col, Button } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";
import { Search as SearchIcon, Replay as ReplayIcon } from "@material-ui/icons";
// import Autosuggest from "react-autosuggest";
// import Calendar from "react-calendar";

/**
  * User imports
  ============================================================================ */
// import SubmitButton from "../submitButton";
// import Card from "../card";
// import { useStickyState } from "../../utils/state";
// import DatePicker from "../datePicker";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    namespace: "Search",
    request: "find currencies",
    advancedSearch: false,
    placeholder: "SEARCH...",
    captionAdvancedSearch: "Advanced search",
    onSubmit: () => {},
  }; // TODO: Adjust this

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { search } = props.barrelGetters;
  const { setSearch } = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]); // adjust namespace

  /** 
    * Hooks
    ============================================================================ */
  // const [suggestions, setSuggestions] = useStickyState([], "suggestions");

  /**
    * Create refs
    ============================================================================ */
  const refSearch = React.createRef();

  /**
    * Clear refs
    ============================================================================ */
  useEffect(() => {
    const tempRefSearch = refSearch.current;
    return () => {
      if (tempRefSearch !== null && search !== tempRefSearch.value)
        setSearch(tempRefSearch.value);
    };

    // eslint-disable-next-line
  }, []);

  /** 
    * Yup schema
    ============================================================================ */
  const schema = yup.object({
    search: yup.string().optional(),
  });

  /**
    * Functions
    ============================================================================ */
  const onSubmit = (data) => {
    // findCurrencies(data.search);
    props.onSubmit(data.search);
  };

  /**
    * Form
    ============================================================================ */
  const ComponentForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <>
      <Form noValidate onSubmit={handleSubmit}>
        {/**
         * Search
         */}
        <Form.Group style={{ padding: 0, margin: 0 }}>
          <InputGroup>
            <Form.Control
              type="text"
              placeholder={props.placeholder}
              name="search"
              ref={refSearch}
              value={values.search}
              autoComplete="search"
              onChange={handleChange}
              isInvalid={!!errors.search && touched.search}
            />
            <InputGroup.Append>
              <InputGroup.Text
                id="inputGroupAppend"
                onClick={() => onSubmit(values)}
                style={{
                  cursor: "pointer",
                }}
              >
                <SearchIcon style={{ color: "var(--dark)" }} />
              </InputGroup.Text>
            </InputGroup.Append>
            <Form.Control.Feedback type="invalid">
              {errors.search}
            </Form.Control.Feedback>
          </InputGroup>
          {props.advancedSearch && (
            <div style={{ textAlign: "right" }}>
              <button
                className="button-transparent"
                style={{ margin: "0", padding: "0" }}
              >
                <small className="text-muted">
                  {props.captionAdvancedSearch}
                </small>
              </button>
            </div>
          )}
        </Form.Group>
      </Form>
    </>
  );

  /**
    * Formik return
    ============================================================================ */
  return (
    <Formik
      component={ComponentForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        search: search,
      }}
    />
  );
};
