/**
 * User CSS
 ============================================================================ */
import "./Help.css";

/**
 * System imports
 ============================================================================ */
import React, { lazy } from "react";
import { useTranslation } from "react-i18next";
import { Container, Row, Col } from "react-bootstrap";
import ErrorBoundary from "../errorBoundary";

/**
 * User imports
 ============================================================================ */
import Card from "../card";
import Search from "../search";
import HelpMenuLevel2 from "../helpMenuLevel2";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "Help", searchable: false };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const { language, helpPath1, helpPath2 } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getFileNameFromURI, setHelpPath } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const navigation = [
    {
      name: t("Personal Help"),
      link: "#personal",
    },
    {
      name: t("Business Help"),
      link: "#business",
    },
    {
      name: t("Technical Help"),
      link: "#technical",
    },
  ];

  /**
   * Functions
   ============================================================================ */

  const getJSX = () => {
    // console.log(window.location.pathname);

    let leadFile = getFileNameFromURI();

    let ExternalFile = null;

    ExternalFile =
      leadFile !== null
        ? lazy(() =>
            import(`../locale/${language}/topics/${leadFile}`).catch((err) => {
              // console.log("err:", err);
              return import(`../locale/${language}/topics/common-questions.js`); // TODO: Change LeadLogiIn.js -> _LeadDefault.js
            })
          )
        : null;

    const onHelpPath1Click = (link) => {
      // console.log(link);
      // setHelpPath1(link);
      setHelpPath(1, link);
    };

    return (
      <Card
        title={t("Help center")}
        navigation={navigation}
        headerPadding="20px 20px 10px 20px"
        bodyPadding="20px 20px 10px 20px"
        border={0}
        maxWidthBody="100%"
        navigationdefaultActiveKey={helpPath1}
        onNavigationClick={onHelpPath1Click}
      >
        <Container fluid>
          {/**
           * Search
           */}
          {props.searchable && (
            <Row>
              <Col>
                <div className="header-text">
                  <h1>{t("How can we help?")}</h1>
                </div>
                <div style={{ textAlign: "center", margin: "0 0 35px 0" }}>
                  <div
                    style={{
                      display: "inline-block",
                      width: "100%",
                      maxWidth: "600px",
                    }}
                  >
                    <Search
                      {...args}
                      placeholder={t("Ask a question or search by keyword")}
                    />
                  </div>
                </div>
              </Col>
            </Row>
          )}

          <Row>
            <Col xl={4} style={{}}>
              <HelpMenuLevel2 {...args} />
            </Col>
            <Col xl={8} style={{}}>
              {ExternalFile !== null && (
                <ErrorBoundary>
                  <ExternalFile {...args} />
                </ErrorBoundary>
              )}
            </Col>
          </Row>
        </Container>
      </Card>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
