/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import {
  Navbar,
  Nav,
  Container,
  Row,
  Col,
  // Button,
  // Breadcrumb,
  // NavDropdown,
  // Alert,
} from "react-bootstrap";
import { PowerSettingsNew, AccountCircle } from "@material-ui/icons";

/**
  * User imports
  ============================================================================ */
// import Card from "../card";
import UserEditor from "../userEditor";
import IdleTimer from "../idleTimer";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "MainMenu" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    settings,
    sessionTimeout,
    accountNumber,
    signedIn,
  } = props.barrelGetters;
  // const {} = props.barrelSetters;
  const { getBusinesses } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    return (
      <Container
        fluid
        style={{
          paddingRight: "0",
          paddingLeft: "0",
        }}
      >
        <Row>
          {/*
           * User Menu
           */}
          <Col>
            <Navbar
              // className="card-header"
              // className="navbar-trans"
              // bg="light"
              // variant="light"
              style={{
                borderRadius: 0,
                // background: "transparent !important",
                // color: "#f7f7f7",
                backgroundColor: "var(--light)", // e8f6f3
                boxShadow: "none",
                // whiteSpace: "nowrap",
              }}
            >
              {/**
               * Brand
               */}
              <Navbar.Brand href="/">
                <img
                  src={
                    settings !== null && typeof settings["logo"] !== "undefined"
                      ? settings["logo"]
                      : "/gfx/zlogo_light.png"
                  }
                  alt=""
                  width="auto"
                  height="30"
                  className="d-inline-block align-top"
                />
              </Navbar.Brand>

              {/**
               * User editor
               */}
              <Nav className="mr-auto">
                {signedIn ? (
                  <>
                    {window.location.pathname !== "/auth" ? (
                      <Nav.Link eventKey="profile" href="/profile">
                        <UserEditor {...args} />
                      </Nav.Link>
                    ) : (
                      <Nav.Item>
                        <UserEditor {...args} />
                      </Nav.Item>
                    )}
                  </>
                ) : (
                  <Nav.Item>
                    <strong>{t("Local Currency")}</strong>
                  </Nav.Item>
                )}
              </Nav>

              {/**
               * Register
               */}
              {!signedIn && window.location.pathname !== "/sign-up" && (
                <Nav>
                  <Nav.Link eventKey="sign-out" href="/sign-up">
                    <span style={{ whiteSpace: "nowrap" }}>
                      <span>
                        <AccountCircle
                          style={{
                            fontSize: "1.2rem",
                            paddingBottom: "0.2rem",
                            color: "var(--dark)",
                          }}
                        />
                      </span>

                      <span className="d-none d-sm-inline">
                        &nbsp;
                        {t("Register")}
                      </span>
                    </span>
                  </Nav.Link>
                </Nav>
              )}

              {/**
               * Sign in
               */}
              {!signedIn && window.location.pathname !== "/sign-in" && (
                <Nav>
                  <Nav.Link eventKey="sign-out" href="/sign-in">
                    <span style={{ whiteSpace: "nowrap" }}>
                      <span>
                        <PowerSettingsNew
                          style={{
                            fontSize: "1.2rem",
                            paddingBottom: "0.2rem",
                            color: "var(--dark)",
                          }}
                        />
                      </span>

                      <span className="d-none d-sm-inline">
                        &nbsp;
                        {t("Sign in")}
                      </span>
                    </span>
                  </Nav.Link>
                </Nav>
              )}

              {/**
               * Sign out
               */}
              {signedIn && (
                <Nav>
                  <Nav.Link eventKey="sign-out" href="/sign-out">
                    <span style={{ whiteSpace: "nowrap" }}>
                      <span>
                        <PowerSettingsNew
                          style={{
                            fontSize: "1.2rem",
                            paddingBottom: "0.2rem",
                            color: "var(--dark)",
                          }}
                        />
                      </span>

                      <span className="d-none d-sm-inline">
                        &nbsp;
                        {t("Sign out")}
                      </span>
                    </span>
                  </Nav.Link>
                </Nav>
              )}

              {/**
               * Idle timer
               */}
              {signedIn && (
                <Nav>
                  <Nav.Item>
                    <IdleTimer timeout={sessionTimeout} {...args} />
                  </Nav.Item>
                </Nav>
              )}
            </Navbar>
          </Col>
        </Row>

        {/*
         * Account Menu
         */}
        {signedIn && window.location.pathname !== "/auth" && (
          <Row>
            <Col>
              <Navbar
                collapseOnSelect
                expand="sm"
                // className="card-header"
                // className="navbar-trans"
                // bg="dark"
                variant="dark"
                style={{
                  borderRadius: 0,
                  background: "transparent !important",
                  backgroundColor: "var(--dark)",
                  boxShadow: "none",
                }}
              >
                <Nav>
                  <Nav.Link
                    className="nav-link-2"
                    eventKey="balance"
                    href="/balance"
                    style={{
                      fontWeight:
                        window.location.pathname === "/balance"
                          ? "bold"
                          : "normal",
                    }}
                  >
                    {t("Balance")}
                  </Nav.Link>
                </Nav>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                  <Nav className="mr-auto">
                    {accountNumber !== null && (
                      <>
                        <Nav.Link
                          className="nav-link-2"
                          eventKey="transfer"
                          href="/transfer"
                          style={{
                            fontWeight:
                              window.location.pathname === "/transfer"
                                ? "bold"
                                : "normal",
                          }}
                        >
                          {t("Transfer")}
                        </Nav.Link>
                        <Nav.Link
                          className="nav-link-2"
                          eventKey="my-business"
                          href="#"
                          onClick={() => getBusinesses(1)}
                          style={{
                            fontWeight:
                              window.location.pathname === "/my-business"
                                ? "bold"
                                : "normal",
                          }}
                        >
                          {t("Business")}
                        </Nav.Link>

                        {/**
                         * Currency
                         */}
                        {settings !== null &&
                          typeof settings["currencies"] !== "undefined" &&
                          (settings["currencies"] === null ||
                            settings["currencies"].length === 0 ||
                            settings["currencies"].length > 1) && (
                            <Nav.Link
                              className="nav-link-2"
                              eventKey="currency"
                              href="/currency"
                              style={{
                                fontWeight:
                                  window.location.pathname === "/currency"
                                    ? "bold"
                                    : "normal",
                              }}
                            >
                              {t("Currency")}
                            </Nav.Link>
                          )}
                      </>
                    )}
                  </Nav>
                </Navbar.Collapse>
              </Navbar>
            </Col>
          </Row>
        )}
      </Container>
    );
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
