/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Form, InputGroup } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";

/**
  * User imports
  ============================================================================ */
// import Card from "../card";
import SubmitButton from "../submitButton";

// import { useStickyState } from "../../utils/state";

export default (args) => {
  /**
   * Default Properties
   */
  let defaults = {
    namespace: "BusinessRegistration",
    request: "register business",
  };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   */
  const {
    accountNumber,
    businessRegistrationAddTitle,
    businessRegistrationAddDescription,
  } = props.barrelGetters;
  const {
    setBusinessRegistrationAddTitle,
    setBusinessRegistrationAddDescription,
  } = props.barrelSetters;
  const {
    // format,
    // parseLocaleNumber,
    sendRequest,
    getReviser,
    getWorkplace,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  // const userDataParsed = JSON.parse(userData);
  // const userDataParsed = userData;
  const { t } = useTranslation([props.namespace]);

  const refBusinessTitle = React.createRef();
  const refBusinessDescription = React.createRef();

  useEffect(() => {
    const tempRefBusinessTitle = refBusinessTitle.current;
    const tempRefBusinessDescription = refBusinessDescription.current;
    return () => {
      if (
        tempRefBusinessTitle !== null &&
        businessRegistrationAddTitle !== tempRefBusinessTitle.value
      )
        setBusinessRegistrationAddTitle(tempRefBusinessTitle.value);

      if (
        tempRefBusinessDescription !== null &&
        businessRegistrationAddDescription !== tempRefBusinessDescription.value
      )
        setBusinessRegistrationAddDescription(tempRefBusinessDescription.value);
    };

    // eslint-disable-next-line
  }, []);

  // const [addOwnerAsShareholder, setAddOwnerAsShareholder] = useStickyState(
  //   false,
  //   "addOwnerAsShareholder"
  // );

  const schema = yup.object({
    businessTitle: yup
      .string()
      .required(t("Business title is a required field"))
      .min(3, t("Business title too short"))
      .max(70, t("Business title too long"))
      .trim(),
    businessDescription: yup.string().optional(),
  });

  /**
   * On business registration
   * @param {object} data
   */
  const onSubmit = (data) => {
    data.reviser = getReviser();
    data.workplace = getWorkplace();
    data.uid = accountNumber;

    // console.log("REGISTER BUSINESS DATA:", data);

    // onBusinessRegistration({ req: "register business", data: data });
    sendRequest({ req: props.request, data: data });
  };

  const RegisterBusinessForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    errors,
  }) => (
    <Form noValidate onSubmit={handleSubmit}>
      {/* Business Title (Business ID) */}
      <Form.Group controlId="validationFormikDestinationAccountID">
        <Form.Label>{t("Business Title")}</Form.Label>
        <InputGroup>
          <Form.Control
            type="text"
            placeholder=""
            aria-describedby="inputGroupPrepend"
            name="businessTitle"
            ref={refBusinessTitle}
            value={values.businessTitle}
            onChange={handleChange}
            isInvalid={!!errors.businessTitle && touched.businessTitle}
          />
          <Form.Control.Feedback type="invalid">
            {errors.businessTitle}
          </Form.Control.Feedback>
        </InputGroup>
      </Form.Group>

      {/* Business Description */}
      <Form.Group controlId="businessDescription">
        <Form.Label>{t("Business Description")}</Form.Label>
        <Form.Control
          as="textarea"
          rows="3"
          name="businessDescription"
          ref={refBusinessDescription}
          value={values.businessDescription}
          onChange={handleChange}
        />
      </Form.Group>

      <SubmitButton
        {...args}
        caption={t("Submit")}
        request={props.request}
        namespace={props.namespace}
      />
    </Form>
  );

  return (
    <>
      <Formik
        component={RegisterBusinessForm}
        validationSchema={schema}
        onSubmit={onSubmit}
        initialValues={{
          businessTitle: businessRegistrationAddTitle,
          businessDescription: businessRegistrationAddDescription,
        }}
      />
    </>
  );
};
