/**
 * CSS
 ============================================================================ */
import "./Rating.css";

/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Form, Container, Row, Col } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
// import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "Component" }; // TODO: adjust namespace

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */
  const getJSX = () => {
    return (
      <Container
        fluid
        style={{
          fontSize: "0.9rem",
          textAlign: "center",
          whiteSpace: "nowrap",
        }}
      >
        <Row>
          <Col style={{ padding: "0" }}>
            <Form.Check
              custom
              inline
              type="radio"
              label=""
              name="rating"
              id="rating1"
            />
          </Col>
          <Col style={{ padding: "0" }}>
            <Form.Check
              custom
              inline
              type="radio"
              label=""
              name="rating"
              id="rating2"
            />
          </Col>
          <Col style={{ padding: "0" }}>
            <Form.Check
              custom
              inline
              type="radio"
              label=""
              name="rating"
              id="rating3"
            />
          </Col>
          <Col style={{ padding: "0" }}>
            <Form.Check
              custom
              inline
              type="radio"
              label=""
              name="rating"
              id="rating4"
            />
          </Col>
          <Col style={{ padding: "0" }}>
            <Form.Check
              custom
              inline
              type="radio"
              label=""
              name="rating"
              id="rating5"
            />
          </Col>
        </Row>
        <Row style={{ marginTop: "-0.3rem" }}>
          <Col style={{ padding: "0" }}>1</Col>
          <Col style={{ padding: "0" }}>2</Col>
          <Col style={{ padding: "0" }}>3</Col>
          <Col style={{ padding: "0" }}>4</Col>
          <Col style={{ padding: "0" }}>5</Col>
        </Row>
        <Row>
          <Col style={{ padding: "0" }}>Not helpful</Col>
          <Col style={{ padding: "0" }}></Col>
          <Col style={{ padding: "0" }}></Col>
          <Col style={{ padding: "0" }}></Col>
          <Col style={{ padding: "0" }}>Very helpful</Col>
        </Row>
      </Container>
    );
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
