const dev = {
  uri: {
    ENDPOINT: "http://localhost:9000",
    SITEMODE: "DEVELOPMENT",
  },
};

const dev2 = {
  uri: {
    ENDPOINT: "http://192.168.2.100:5000",
    SITEMODE: "DEVELOPMENT2",
  },
};

const staging = {
  uri: {
    ENDPOINT: "https://dev.local-currency.com",
    SITEMODE: "STAGING",
  },
};

const prod = {
  uri: {
    ENDPOINT: "https://local-currency.com",
    SITEMODE: "PRODUCTION",
  },
};

const hostname = window && window.location && window.location.hostname;

let config;
switch (hostname) {
  case "localhost":
    config = dev;
    break;
  case "192.168.2.100":
    config = dev2;
    break;
  case "dev.local-currency.com":
    config = staging;
    break;
  case "local-currency.com":
    config = prod;
    break;
  default:
    config = dev;
    break;
}

export default {
  ...config,
};
