/**
 * CSS
 ============================================================================ */
// import "bootstrap/dist/css/bootstrap.min.css";
import "./Main.css";

/**
 * System imports
 ============================================================================ */
import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Container, Row, Col } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
import { useTimeout } from "../../utils/useTimeout";
// import { useScrollPosition } from "../../utils/useScrollPosition";
import Routes from "./Routes";
import Leads from "../leads";
import Toasts from "../toasts";

/**
 * Component: Main
 ============================================================================ */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Main" };

  /**
      * Properties
      ============================================================================ */
  const props = Object.assign(defaults, args);
  const barrels = args;

  /**
   * Module vars
   ============================================================================ */
  const now = new Date();

  /**
      * Barrels
      ============================================================================ */
  const {
    session,
    serverResponseReceived,
    signedIn,
    userData,
    dailyAllowanceDispatched,
    accountNumber,
    cartID,
    cartContent,
    validateTokenEmail,
    passwordResetTokenValidated,
    validateTokenPasswordReset,
    connected,
    // isExternalRequest,
    clearRefs,
    loggingOut,
    scrollPositionY,
    // hideOnScroll,
    cartProcessingStatus,
  } = props.barrelGetters;
  const {
    setRequestTimedOut,
    setUpdateInterval,
    setDailyAllowanceDispatched,
    setRequest,
    // setIsExternalRequest,
    setUserEditorFirstname,
    setUserEditorLastname,
    setUserEditorEmail,
    setUserEditorPassword,
    setRedirectTo,

    // Currency registration
    setCurrencyCode,
    setCurrencyName,
    setCurrencyRegion,
    setCurrencyNewRate,

    // Business registration
    setBusinessRegistrationAddTitle,
    setBusinessRegistrationAddDescription,

    // Purpose registration
    setPurposeRegistrationTitle,
    setPurposeRegistrationDescription,
    setPurposeRegistrationKeywords,

    // Contact
    setContactSubject,
    setContactMessage,

    // Date range
    setDateRangeStart,
    setDateRangeEnd,

    // Search
    setSearch,

    //
    setClearRefs,

    // setHideOnScroll,
  } = props.barrelSetters;
  const {
    // processUserResponse,
    getIP,
    saveCartID,
    getCartPurposes,
    // updateLastSeenUser,
    getAccountData,
    sendRequest,
    sendEmailValidationRequest,
    sendPasswordResetRequest,
    showMessage,
    getCart,
    // setScrollPosition,
  } = props.barrelFunctions;
  const {
    onRedirect,
    onSessionStart,
    // onSessionEnd,
  } = props.barrelEventHandlers;

  const { t } = useTranslation(props.namespace);

  /** 
   * Custom hooks
   ============================================================================ */

  // useScrollPosition(
  //   ({ prevPos, currPos }) => {
  //     const isShow = currPos.y > prevPos.y;
  //     if (isShow !== hideOnScroll) setHideOnScroll(isShow);
  //   },
  //   [hideOnScroll]
  // );

  /** 
   * Effect hooks
   ============================================================================ */

  /**
   * On mount / on unmount
   */
  useEffect(() => {
    getIP();

    // saveCartID();
    // getCart();

    return () => {
      // on unmount
    };

    // eslint-disable-next-line
  }, []);

  /**
   * On session ID change
   */
  useEffect(() => {
    if (session === null) return;

    onSessionStart();

    // eslint-disable-next-line
  }, [session]);

  /**
   * On connected
   */
  useEffect(() => {
    // eslint-disable-next-line
  }, [connected]);

  /**
   * On request timeout
   */
  useTimeout(
    () => {
      setRequestTimedOut(true);
      setRequest(null);
      showMessage("REQUEST_EXPIRED", "info");
    },
    serverResponseReceived === false ? 7000 : -1 // 7 sec till socket request timeout
  );

  /**
   * Map user data to user editor vars
   */
  useEffect(() => {
    if (userData === null) return;
    setUserEditorFirstname(userData.firstname);
    setUserEditorLastname(userData.lastname);
    setUserEditorEmail(userData.email);
    setUserEditorPassword("");
    // eslint-disable-next-line
  }, [JSON.stringify(userData)]);

  /**
   * On signed in status change
   */
  useEffect(() => {
    if (!signedIn) return;

    setUpdateInterval(-1);

    // Dispatch daily allowance
    if (accountNumber !== null && !dailyAllowanceDispatched) {
      getAccountData();
      sendRequest({
        req: "dispatch daily allowance",
        data: { uid: accountNumber },
      });
      setDailyAllowanceDispatched(true);
    }

    /**
     * Handle external request
     */
    // let redirectTarget;
    if (accountNumber !== null) {
      if (cartID !== null) {
        // redirectTarget = "/auth";
        // setIsExternalRequest(false);
        getCart();
        onRedirect("/auth");
      }
    } else {
      if (window.location.pathname !== "/sign-out") {
        if (
          userData === null ||
          userData.status !== 1 ||
          typeof userData.status === "undefined"
        ) {
          showMessage("EMAIL_NOT_YET_VALIDATED", "info");
          onRedirect("/sign-out");
          return;
        } else {
          onRedirect("/account-registration");
        }
      }
    }

    // eslint-disable-next-line
  }, [signedIn]);

  /**
   * On account number change
   */
  useEffect(() => {
    // if (accountNumber === null) return;

    getCart();

    // eslint-disable-next-line
  }, [accountNumber]);

  /**
   * On account number change
   */
  // useEffect(() => {
  //   if (cartProcessingStatus === null) return;

  //   console.log("cartProcessingStatus: ", cartProcessingStatus);

  //   // eslint-disable-next-line
  // }, [cartProcessingStatus]);

  /**
   * Clear refs
   */
  useEffect(() => {
    if (clearRefs === false) return;

    // Business registration
    setBusinessRegistrationAddTitle("");
    setBusinessRegistrationAddDescription("");

    // Purpose registration
    setPurposeRegistrationTitle("");
    setPurposeRegistrationDescription("");
    setPurposeRegistrationKeywords("");

    // Currency registration
    setCurrencyCode("");
    setCurrencyName("");
    setCurrencyRegion("");
    setCurrencyNewRate("");

    // Contact
    setContactSubject("");
    setContactMessage("");

    // Date range
    setDateRangeStart(now.setDate(now.getDate() - 7));
    setDateRangeEnd("today");

    // Search
    setSearch("");

    setClearRefs(false);

    // eslint-disable-next-line
  }, [clearRefs]);

  /**
   * On external request
   */
  // useEffect(() => {
  //   if (externalRequest === null || accountNumber === null) return;

  //   const data = {
  //     customer: accountNumber,
  //     cart: externalRequest.cart,
  //   };

  //   sendRequest({ req: "get cart", data: data });

  //   // eslint-disable-next-line
  // }, [JSON.stringify(externalRequest)]);

  /**
   * On cart content change
   */
  useEffect(() => {
    if (!signedIn || cartContent === null) return;

    getCartPurposes();

    // eslint-disable-next-line
  }, [JSON.stringify(cartContent)]);

  /**
   * On email validation token
   */
  useEffect(() => {
    if (validateTokenEmail === null) return;

    sendEmailValidationRequest(JSON.parse(validateTokenEmail));

    // eslint-disable-next-line
  }, [validateTokenEmail]);

  /**
   * On email validation token
   */
  useEffect(() => {
    if (passwordResetTokenValidated === null) return;

    onRedirect("/password");

    // eslint-disable-next-line
  }, [passwordResetTokenValidated]);

  /**
   * On password reset validation token
   */
  useEffect(() => {
    if (validateTokenPasswordReset === null) return;

    sendPasswordResetRequest(JSON.parse(validateTokenPasswordReset));

    // eslint-disable-next-line
  }, [validateTokenPasswordReset]);

  const getJSX = () => {
    return (
      <>
        <Container fluid>
          <Row>
            {/** Left column */}
            <Col xl={4} lg={6} className="d-none d-lg-block" style={{}}>
              <Leads {...barrels} />
            </Col>

            {/** Middle column */}
            <Col
              lg={6}
              md={12}
              style={{ padding: 0, backgroundColor: "Transparent" }}
            >
              <Routes {...barrels} />
            </Col>

            {/** Right column */}
            <Col
              className="d-none d-xl-block"
              style={{ paddingLeft: 0, paddingRight: 0 }}
            >
              <Toasts {...barrels} />
            </Col>
          </Row>
        </Container>
      </>
    );
  };

  return <>{getJSX()}</>;
};
