/**
 * CSS
 ============================================================================ */
import "./Overlay.css";

/**
 * System imports
 ============================================================================ */
import React, { cloneElement } from "react";
import { useTranslation } from "react-i18next";

/**
  * User imports
  ============================================================================ */
import { useTimeout } from "../../utils/useTimeout";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = {
    namespace: "Overlay",
    visible: false,
    variant: "info",
    dismissible: false,
    onClose: () => {},
  };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  // console.log("dismissible:", props.dismissible);

  /**
    * Barrels
    ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  // const { t } = useTranslation([props.namespace]);

  useTimeout(
    () => {
      props.onClose();
    },
    !props.dismissible ? 5000 : -1 // millisec till overlay closes, -1 never
  );

  /**
    * Functions
    ============================================================================ */
  const getJSX = () => {
    if (props.visible) {
      let className = "custom-overlay";

      /**
       * Implement property: dismissible
       */
      if (props.dismissible) {
        className += " alert-dismissible";
      }

      /**
       * Implement property: variant
       */
      switch (props.variant) {
        case "danger":
          className += " alert-danger";
          break;

        default:
          className += " alert-info";
          break;
      }
      let key = 0;
      return (
        <div className={className}>
          {props.dismissible && (
            <button type="button" className="close" onMouseDown={props.onClose}>
              <span aria-hidden="true"></span>
              <span className="sr-only">Close alert</span>
            </button>
          )}
          {React.Children.map(args.children, (child) => {
            if (typeof child !== "object") {
              return child;
            } else {
              key++;
              return cloneElement(child, {
                ...args,
                styles: null,
                key: key,
              });
            }
          })}
        </div>
      );
    } else {
      return <></>;
    }
  };

  /**
    * Return
    ============================================================================ */
  return <>{getJSX()}</>;
};
