// import { useTranslation } from "react-i18next";

export default ({ args, barrels }) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {};

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const { debug, cartContent } = barrels.barrelGetters;
  const {
    setRequest,
    setUID,
    setAccountNumber,
    setRedirectTo,
    setCartProcessingStatus,
  } = barrels.barrelSetters;
  const { showMessage } = barrels.barrelFunctions;
  const { onRedirect } = barrels.barrelEventHandlers;

  //   const { t } = useTranslation("Errors");
  const { req, error, context } = props;
  if (debug) console.log(`ERROR: ${req}`, error, context, props, barrels);
  // const { showMessage } = barrels.barrelFunctions;

  let errorFound = false;

  /** 
   * Request event dispatcher
   ============================================================================ */
  const onError = (data, callback) => {
    if (errorFound) return;
    const [res, status] = data;
    // console.log(res, status);

    if (
      (res === req && status === error.code) ||
      (res === "*" && status === error.code) ||
      res === "unknown"
    ) {
      callback();
      setRequest(null);
      errorFound = true;
      return true;
    }
    return false;
  };

  /** 
   * Response handlers
  ============================================================================ */

  onError(["*", "INVALID_SESSION"], () => {
    showMessage("SESSION_TIMED_OUT", "info", false);
    setRedirectTo("/sign-out");
    return true;
  });

  /**
   * Transfer errors
   */
  onError(["transfer U2S", "NO_ROWS_CHANGED"], () => {
    showMessage("INVALID_TRANSFER", "error");
    return true;
  });

  onError(["transfer U2U", "NO_ROWS_CHANGED"], () => {
    showMessage("INVALID_TRANSFER", "error");
    return true;
  });

  onError(["transfer U2B", "NO_ROWS_CHANGED"], () => {
    if (cartContent !== null) {
      setCartProcessingStatus(false);
    } else {
      showMessage("INVALID_TRANSFER", "error");
    }
    return true;
  });

  onError(["register user", "NO_ROWS_CHANGED"], () => {
    showMessage("INVALID_USER_REGISTRATION", "error");
    return true;
  });

  onError(["register user language", "NO_ROWS_CHANGED"], () => {
    // user selected the same language again - just ignore
    return true;
  });

  onError(["validate email token", "NO_ROWS_CHANGED"], () => {
    showMessage("TOKEN_VALIDATION_FAILED", "error");
    return true;
  });

  onError(["validate password reset token", "NO_ROWS_CHANGED"], () => {
    showMessage("TOKEN_VALIDATION_FAILED", "error");
    return true;
  });

  onError(["reset password", "NO_ROWS_CHANGED"], () => {
    // showMessage("REQUEST_ALREADY_SENT", "info");
    return true;
  });

  onError(["register user language", "NO_ROWS_CHANGED"], () => {
    // User selected the same language again - that's ok
    return true;
  });

  onError(["update user", "NO_ROWS_CHANGED"], () => {
    // User selected the same password again - that's ok
    return true;
  });

  // onError(["update last seen user", "NO_ROWS_CHANGED"], () => {
  //   // Same last seen time was submitted (shouldn't be possible) - ignore for now
  //   return true;
  // });

  onError(["sign in user", "NO_ROWS_CHANGED"], () => {
    showMessage("USER_NOT_FOUND", "error");
    return true;
  });

  onError(["change password", "NO_ROWS_CHANGED"], () => {
    // User reset password to the actual password
    showMessage("SAME_PASSWORD_RESET", "info");
    return true;
  });

  onError(["submit tan", "NO_ROWS_CHANGED"], () => {
    // User reset password to the actual password
    showMessage("INVALID_TAN", "error", false);
    return true;
  });

  onError(["get cart", "NO_ROWS_CHANGED"], () => {
    // User accessed the same cart again - it's fine
    return true;
  });

  onError(["transfer V2U", "NO_ROWS_CHANGED"], () => {
    // Allowance already dispatched today - totally fine
    return true;
  });

  onError(["register user currency", "NO_ROWS_CHANGED"], () => {
    // The same currency was selected again - no problem
    return true;
  });

  onError(["end session", "NO_ROWS_CHANGED"], () => {
    // Session updated to the same data ??
    return true;
  });

  onError(["register currency", "NO_ROWS_CHANGED"], () => {
    showMessage("DUPLICATE_CURRENCY_REGISTRATION", "error");
    return true;
  });

  onError(["update purpose property", "NO_ROWS_CHANGED"], () => {
    showMessage("DUPLICATE_PURPOSE_PROPERTY", "error");
    return true;
  });

  onError(["register purpose", "ER_DUP_ENTRY"], () => {
    showMessage("DUPLICATE_PURPOSE_PROPERTY", "error");
    return true;
  });

  // onError(["get user sequence", "NO_ROWS_CHANGED"], () => {
  //   // Session updated to the same data ??
  //   return true;
  // });

  onError(["register account", "ER_DUP_ENTRY"], () => {
    setUID(null);
    setAccountNumber(null);
    showMessage("DUPLICATE_ACCOUNT_REGISTRATION", "error");
    return true;
  });

  onError(
    ["register account", "ER_FOREIGN_DUPLICATE_KEY_WITH_CHILD_INFO"],
    () => {
      setUID(null);
      setAccountNumber(null);
      showMessage("DUPLICATE_ACCOUNT_REGISTRATION", "error");
      return true;
    }
  );

  onError(["register shareholder", "ER_NO_REFERENCED_ROW_2"], () => {
    showMessage("USER_NOT_FOUND", "error");
    return true;
  });

  onError(["reset password", "ER_NO_REFERENCED_ROW_2"], () => {
    // Reset password email not found - might be an attack - imitate the behavior as if the link was sent
    onRedirect("/password");
    return true;
  });

  onError(["*", "ER_CANT_CREATE_THREAD"], () => {
    showMessage("NO_RESPONSE_FROM_SERVER", "error");
    return true;
  });

  onError(["unknown"], () => {
    showMessage(error.code, "error");
    return true;
  });

  return false;
};
