/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";
import { Accordion } from "react-bootstrap";

/**
 * User imports
 ============================================================================ */
import Transactions from "../transactions";
import Shares from "../shares";
import AccordionCard from "../accordionCard";
import Balance from "../balance";
import CopyableField from "../copyableField";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   */
  let defaults = { namespace: "AccountContainer", paddingBody: "20px 0 0 0" };

  /**
   * Properties
   */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   */
  const { accountNumber, accordionView } = props.barrelGetters;
  const {
    setRedirectTo,
    setAccordionView,
    setResponseProps,
    setClearRefs,
  } = props.barrelSetters;
  const { getAccountData, getTransactions, getShares } = props.barrelFunctions;

  const { t } = useTranslation(props.namespace);

  if (accountNumber === null) setRedirectTo("/sign-out");

  /**
   *
   * @param {*} view
   */
  const toggleView = (view) => {
    const newView = accordionView !== view ? view : null;

    setResponseProps(null);
    setAccordionView(newView);
    setClearRefs(true);

    if (newView !== null) {
      switch (view) {
        case "balance":
          getAccountData();
          break;

        case "transactions":
          getTransactions(1);
          break;

        case "shares":
          getShares(1);
          break;

        default:
          break;
      }
    }
  };

  /**
   * Return
   ============================================================================ */
  return (
    <>
      <Accordion defaultActiveKey={accordionView}>
        {/**
         * Account number
         */}
        <AccordionCard
          title={t("Account number")}
          eventkey="accountNumber"
          active={accordionView}
          paddingBody={props.paddingBody}
          onClick={() => toggleView("accountNumber")}
        >
          <CopyableField value={accountNumber} {...args} />
        </AccordionCard>

        {/**
         * Balance
         */}
        <AccordionCard
          title={t("Balance")}
          eventkey="balance"
          active={accordionView}
          paddingBody={props.paddingBody}
          onClick={() => toggleView("balance")}
        >
          <Balance {...args} />
        </AccordionCard>

        {/**
         * Transactions
         */}
        <AccordionCard
          title={t("Transactions")}
          eventkey="transactions"
          active={accordionView}
          paddingBody={props.paddingBody}
          onClick={() => toggleView("transactions")}
        >
          <Transactions {...args} />
        </AccordionCard>

        {/**
         * Shares
         */}
        <AccordionCard
          title={t("Shares")}
          eventkey="shares"
          active={accordionView}
          paddingBody={props.paddingBody}
          onClick={() => toggleView("shares")}
        >
          <Shares {...args} />
        </AccordionCard>
      </Accordion>
    </>
  );
};
