/**
 * System imports
 ============================================================================ */
import React, { useEffect } from "react";
import { Form, InputGroup } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";
import { useTranslation } from "react-i18next";
import flatpickr from "flatpickr";
// import { Latvian } from "flatpickr/dist/l10n/lv.js";
import * as language from "flatpickr/dist/l10n"; // DO NOT REMOVE !!!
import { DateRange } from "@material-ui/icons";

/**
   * User imports
   ============================================================================ */
// import SubmitButton from "../submitButton";
// import Card from "../card";

/**
 * Default export
 */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = {
    namespace: "DatePickerWrapper",
    request: "get something",
    value: "",
    placeholder: "Date",
  }; // TODO: Adjust this

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
     * Destructured vars
     ============================================================================ */
  const { t } = useTranslation([props.namespace]); // adjust namespace

  /**
    * Create refs
    ============================================================================ */
  const refDatePicker = React.createRef();

  /**
    * States
    ============================================================================ */
  // const [isOpen, setIsOpen] = useState(false);

  /**
    * Module vars
    ============================================================================ */
  let fp = null; // flatPicker instance

  /**
    * Effects
    ============================================================================ */
  useEffect(() => {
    fp = flatpickr(refDatePicker.current, props.options);

    return () => {
      // on unmount
      fp.destroy();
      fp = null;
    };

    // eslint-disable-next-line
  }, []);

  // useEffect(() => {
  //   if (fp === null) return;

  //   console.log("isOpen:", isOpen);

  //   // isOpen ? fp.open() : fp.close();

  //   // eslint-disable-next-line
  // }, [isOpen]);

  // useEffect(() => {
  //   if (fp !== null) return;

  //   fp = flatpickr(refDatePicker.current, props.options);

  //   // eslint-disable-next-line
  // });

  /** 
     * Yup schema
     ============================================================================ */
  const schema = yup.object({
    datePicker: yup.string().required(t("Date is required")),
  });

  /**
     * Functions
     ============================================================================ */
  const onSubmit = (data) => {
    console.log(data);
  };

  /**
     * Form
     ============================================================================ */
  const ComponentForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    validateForm,
    errors,
  }) => (
    <InputGroup>
      <Form.Control
        type="string"
        placeholder={props.placeholder}
        aria-describedby="inputGroupPrepend"
        ref={refDatePicker}
        name="datePicker"
        autoComplete="off"
        value={values.datePicker}
        onChange={handleChange}
        isInvalid={!!errors.datePicker && touched.datePicker}
      />
      <InputGroup.Append>
        <InputGroup.Text
          onClick={() => {
            // console.log("click");
            if (fp !== null) fp.open();

            // setIsOpen(!isOpen);
          }}
          style={{
            cursor: "pointer",
          }}
        >
          <DateRange style={{ color: "var(--dark)" }} />
        </InputGroup.Text>
      </InputGroup.Append>
      <Form.Control.Feedback type="invalid">
        {errors.datePicker}
      </Form.Control.Feedback>
    </InputGroup>
  );

  /**
     * Formik return
     ============================================================================ */
  return (
    <Formik
      component={ComponentForm}
      validationSchema={schema}
      onSubmit={onSubmit}
      initialValues={{
        datePicker: props.value,
      }}
    />
  );
};
