// import settings from "../settings.json";

export default ({ args, barrels }) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = {};

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  const { debug, info: msgInfo } = barrels.barrelGetters;
  const { setRequest, setReconnecting } = barrels.barrelSetters;
  const { showMessage, hideMessage } = barrels.barrelFunctions;
  // const {} = barrels.barrelEventHandlers;

  /**
   * Hooks
   ============================================================================ */
  const { req } = props; // req, status

  /**
   * Log response
   */
  if (debug)
    console.log(`system response: ${req} (${props.status})`, props, barrels);

  let responseFound = false;

  /** 
   * Request event dispatcher
   ============================================================================ */
  const onResponse = (data, callback) => {
    if (responseFound) return;
    const [res, status, name = null] = data;

    if (
      (res === "*" && status === props.status) ||
      (res === props.req && status === props.status) ||
      res === "unknown"
    ) {
      if (callback()) {
        setRequest(null);
      }
      if (name === null) responseFound = true;
      // return true;
    }
    // return false;
  };

  /** 
   * Response handlers
  ============================================================================ */

  // onResponse(["*", "continue", "VALIDATE SESSION"], () => {
  //   if (typeof props.token !== "undefined") {
  //     console.log("Renewing session...");

  //     onSessionEnd();
  //     setSession(props.token);
  //   }

  //   return false;
  // });

  /**
   * Reconnect attempt (continue)
   */
  onResponse(["reconnect attempt", "continue"], () => {
    // if (msgInfo !== "RECONNECT_ATTEMPT")
    //   showMessage("RECONNECT_ATTEMPT", "info");
    return true;
  });

  /**
   * Reconnect attempt (continue)
   */
  onResponse(["reconnecting", "continue"], () => {
    if (msgInfo !== "RECONNECTING") {
      setReconnecting(true);
      showMessage("RECONNECTING", "info");
    }
    return true;
  });

  /**
   * Reconnect attempt (continue)
   */
  onResponse(["reconnect", "success"], () => {
    setReconnecting(false);
    hideMessage();
    // hideMessage("error");
    // hideMessage("info");
    return true;
  });

  onResponse(["unknown"], () => {
    return false;
  });

  return responseFound;
};
