/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation, Trans } from "react-i18next";
import { Form, InputGroup } from "react-bootstrap";
import { Formik } from "formik";
import * as yup from "yup";

/**
  * User imports
  ============================================================================ */
import Card from "../card";
import SubmitButton from "../submitButton";
import TanInput from "../tanInput";
import { useEffect } from "react";

/**
 * Default export
 */
export default (args) => {
  /**
    * Default Properties
    ============================================================================ */
  let defaults = { namespace: "Checkout" };

  /**
    * Properties
    ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
    * Barrels
    ============================================================================ */
  const {
    // externalRequest,
    // accountNumber,
    cartProps,
    cartContent,
    cartPurposes,
    tanRequired,
    userData,
    language,
    accountNumber,
    currencyAbbr,
    cartID,
    cartProcessingStatus,
  } = props.barrelGetters;
  const { setTransferRequest, setCartProcessingStatus } = props.barrelSetters;
  const { format, sendRequest, formatDate } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
    * Destructured vars
    ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  const schema = yup.object({});

  const getCartPurposes = () => {
    if (cartContent === null) return false;

    let priceTotal = 0;
    let priceReal = 0;

    let purposeID = [];
    let amount = [];
    let currency = [];
    let fromAccount = [];

    cartContent.map((element) => {
      priceTotal += element.price * element.amount;
      let product_found = "N";

      if (cartPurposes !== null) {
        let product = cartPurposes.find(
          (products) => products.purpose_id === element.product
        );

        if (typeof product !== "undefined") {
          product_found = "Y";
          priceReal += element.price * element.amount;
        }
        // console.log("product:", product);
      }

      /**
       * In current implementation if product could be added to the cart,
       * then subsequential deactivation is ignored.
       */
      purposeID.push(element.product);
      amount.push(element.price * element.amount);
      currency.push(currencyAbbr);
      fromAccount.push("acc_curr");
    });

    return { purposeID, amount, currency, fromAccount };
  };

  const Cart = () => {
    if (cartProps === null) return <></>;

    let priceTotal = 0; // Price of active AND inactive purposes
    let priceReal = 0; // Price of active purposes

    const businessCreated = formatDate(cartProps.business_created, {
      month: "numeric",
      year: "numeric",
    });

    return (
      <>
        {cartContent !== null &&
          cartContent.map((element, key) => {
            priceTotal += element.price * element.amount;
            let product_found = "N";

            if (cartPurposes !== null) {
              let product = cartPurposes.find(
                (products) => products.purpose_id === element.product
              );

              if (typeof product !== "undefined") {
                product_found = "Y";
                priceReal += element.price * element.amount;
              }
              // console.log("product:", product);
            }

            //   if (typeof product !== "undefined") {
            //     product_found = product.title;
            //   }
            // }

            return (
              <div key={key}>
                {false && (
                  <>
                    <div>product: {element.product}</div>
                    <div>price: {element.price}</div>
                    <div>amount: {element.amount}</div>
                    <div>found: {product_found}</div>
                    <br />
                  </>
                )}
              </div>
            );
          })}

        <div style={{ fontWeight: "bold" }}>{t("Merchant")}</div>
        <div style={{}}>{cartProps.merchant}</div>
        <div style={{}}>{cartProps.business_title}</div>
        <div style={{ marginBottom: "20px" }}>
          <Trans
            i18nKey="Checkout:Business created"
            businessCreated={businessCreated}
          >
            Since {{ businessCreated }}
          </Trans>
        </div>

        <div style={{ fontWeight: "bold" }}>{t("Amount")}</div>
        <div style={{ fontSize: "1.5rem" }}>{format(priceTotal)}</div>
      </>
    );
  };

  const onSubmit = (data) => {
    setCartProcessingStatus(null);

    data.transferType = "transfer U2B";
    data.uid = accountNumber;
    data.email = userData.email;
    data.language = language;

    const { amount, currency, purposeID, fromAccount } = getCartPurposes();

    data.amount = amount;
    data.currency = currency;
    data.purposeID = purposeID;
    data.fromAccount = fromAccount;

    data.cart = cartProps.cartid;
    data.merchant = cartProps.merchant;

    setTransferRequest(JSON.stringify(data));

    data = { email: userData.email, language };

    sendRequest({ req: "get tan", data: data });

    // setTransferAmount("");
  };

  const CheckoutForm = ({
    handleSubmit,
    handleChange,
    handleBlur,
    values,
    touched,
    isValid,
    errors,
  }) => (
    <Card title={t("Checkout")} border="0">
      {/* From account */}
      <Form noValidate onSubmit={handleSubmit}>
        <div style={{ marginBottom: "20px" }}>
          <Cart />
        </div>
        {/* Submit button */}
        <SubmitButton
          {...args}
          caption={t("Submit")}
          request="get tan"
          namespace={props.namespace}
        />
      </Form>
    </Card>
  );

  /**
    * Return
    ============================================================================ */
  // return <>{getJSX()}</>;

  return (
    <>
      {cartProcessingStatus === null ? (
        <>
          <TanInput show={tanRequired} size="sm" {...args} />
          <Formik
            component={CheckoutForm}
            validationSchema={schema}
            onSubmit={onSubmit}
            initialValues={{}}
          />
        </>
      ) : (
        <>
          {cartProcessingStatus === true
            ? "Transfer successful"
            : "Transfer error"}
        </>
      )}
    </>
  );
};
