/**
 * System imports
 ============================================================================ */
import React from "react";
import { useTranslation } from "react-i18next";

/**
 * User imports
 ============================================================================ */
import Card from "../card";

/**
 * Default export
 ============================================================================ */
export default (args) => {
  /**
   * Default Properties
   ============================================================================ */
  let defaults = { namespace: "HttpResponseStatusCodes" };

  /**
   * Properties
   ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
   * Barrels
   ============================================================================ */
  // const {} = props.barrelGetters;
  // const {} = props.barrelSetters;
  // const {} = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;

  /**
   * Module vars
   ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
   * Functions
   ============================================================================ */
  const getJSX = () => {
    return <Card title={t("404 Not found")} border={0}></Card>;
  };

  /**
   * Return
   ============================================================================ */
  return <>{getJSX()}</>;
};
